import { AgentMessageEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const agentMessageService = {
    getUsersWithAgentMessages,
}

async function getUsersWithAgentMessages() {
    try {
        const res = await httpService.post(`${AgentMessageEP.getUsers}`)
        if (res.status === 'ok') return res.content
        else throw new Error(res.message)
    } catch (err) {
        throw err
    }
}
