import { ContactRequestEP } from '../config/appConfig'
import { ContactRequest } from '../model/contactRequest/ContactRequest';
import { httpService } from "./http.service";

export const contactRequestService = {
    query,
    createContactRequest,
    removeContactRequest,
    updateContactRequest,
    rejectContactRequest,
}

export async function query(filterBy = {}) {
    try {
        const res = await httpService.get(ContactRequestEP.get, filterBy)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get requests')
    } catch (err) {
        throw err
    }
}

async function removeContactRequest(contactRequestId) {
    try {
        const content = { id: contactRequestId }
        const res = await httpService.post(`${ContactRequestEP.remove}/${contactRequestId}`, content)
        if (res.status === 'ok') return res
        else throw new Error('Cannot remove request')
    } catch (err) {
        throw err
    }
}

async function createContactRequest(contactRequest) {
    try {
        const content = { contactRequest: _createContactRequestModel(contactRequest) }
        const res = await httpService.post(`${ContactRequestEP.create}`, content)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't create contact request")
    } catch (err) {
        throw err
    }
}

async function updateContactRequest(contactRequest) {
    try {
        const res = await httpService.post(`${ContactRequestEP.update}`, contactRequest)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't update contact request")
    } catch (err) {
        throw err
    }
}

async function rejectContactRequest(contactRequest) {
    try {
        const res = await httpService.post(`${ContactRequestEP.reject}`, contactRequest)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't reject contact request")
    } catch (err) {
        throw err
    }
}

function _createContactRequestModel(contactRequest) {
    return new ContactRequest(contactRequest)
}