import "./ContactAdmin.css"
import { Link, useNavigate, useParams } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import { useEffect, useState } from "react"
import { updateContact } from "../../../redux/contact/apiCalls"
import { JsConst } from "../../../constant/JsConst"
import { contactService } from "../../../services/contact.service"
import { FileUploader } from "../../../components/FileUploader"
import { DUMMY_USER_URL } from "../../../config/appConfig"
import { OptionsSelect } from "../../../components/OptionsSelect"
import { constTranslate } from "../../../constant/translate"
import { constContact } from "../../../model/contact/constContact"
import Chart from "../../../components/admin/chart/Chart"
import { constUser } from "../../../model/user/constUser"
import { getUsers } from "../../../redux/user/apiCalls"
import { utilService } from "../../../services/util.service"
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService"
import { userService } from "../../../services/user.service"


export default function ContactAdmin() {
  const { contacts } = useSelector((state) => state.contacts)
  const { currentUser, users } = useSelector((state) => state.user)
  const { territories, jobTitles, categories, companies } = useSelector((state) => state.categoryManager)

  const { contactId } = useParams()
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [pStats, setPStats] = useState([])
  const [contact, setContact] = useState(null)
  const [form, setForm] = useState({
    [JsConst._ID]: contactId || '',
    [JsConst.COMPANY]: '',
    [JsConst.DESC]: '',
    [JsConst.PRICE]: '',
    [JsConst.IN_STOCK]: '',
  })

  useEffect(() => {
    if (!users.length) loadUsers()
  }, [])

  useEffect(() => {
    loadContact()
  }, [contactId, contacts])


  useEffect(() => {
    if (contact) updateForm()
  }, [contact])

  const loadContact = async () => {
    let contact;
    contact = contacts.find(currContact => currContact._id === contactId)
    if (contact) return setContact(contact)
    else {
      contact = await contactService.getContactById(contactId)
      setContact(contact)
    }
  }

  const loadUsers = () => {
    try {
      getUsers(dispatch)
    } catch (err) {
      showErrorMsg('Cannot load users')
    }
  }

  const updateForm = () => {
    setForm((prevState) => ({ ...prevState, ...contact, }))
  }

  const handleChange = ({ target }) => {
    const { value, name } = target
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const handleSelectChange = (name, value) => {
    if (name === constContact.AGENT) {
      if (value === constTranslate.NO_AGENT) {
        return setForm((prevState) => ({ ...prevState, [name]: null }))
      } else {
        const agent = utilService.getAgent(users, value)
        return setForm((prevState) => ({ ...prevState, [name]: agent }))
      }
    } else if (name === constContact.IN_STOCK) {
      return setForm((prevState) => ({ ...prevState, [name]: value === 'true' ? true : false }))
    } else {
      return setForm((prevState) => ({ ...prevState, [name]: value }))
    }
  }

  const onSaveContact = async (ev) => {
    ev.preventDefault()
    try {
      await updateContact(dispatch, form, currentUser)
      showSuccessMsg('Contact updated successfully')
      navigate('/admin/contacts')
    } catch (err) {
      showErrorMsg('Cannot update contact')
    }
  }

  const updateContactImage = (value, name) => {
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const getAgetsUsers = () => {
    const agentUsers = userService.getAgentUsers(users)
    agentUsers.unshift({ fullname: constTranslate.NO_AGENT })
    return agentUsers
  }

  if (!contact) return <div>Loading...</div>
  return (
    <div className="contact">
      <div className="contactTitleContainer">
        <h1 className="contactTitle">Contact</h1>
        <Link to="/admin/new-contact">
          <button className="contactAddButton">Create</button>
        </Link>
      </div>
      <div className="contactTop">
        <div className="contactTopLeft">
          <Chart data={pStats} dataKey="Sales" title="Sales Performance" />
        </div>
        <div className="contactTopRight">
          <div className="contactInfoTop">
            <img className="contactInfoImg" src={contact.img?.url || DUMMY_USER_URL} alt="img" />
            <span className="contactName">{contact[constContact.COMPANY]}</span>
          </div>
          <div className="contactInfoBottom">
            <div className="contactInfoItem">
              <span className="contactInfoKey">{constTranslate.ID}:</span>
              <span className="contactInfoValue">{contact[constContact._ID]}</span>
            </div>
            <div className="contactInfoItem">
              <span className="contactInfoKey">{constTranslate.SALES}:</span>
              <span className="contactInfoValue">5123</span>
            </div>
            <div className="contactInfoItem">
              <span className="contactInfoKey">{constTranslate.IN_STOCK}:</span>
              <span className="contactInfoValue">{contact[constContact.IN_STOCK] ? constTranslate.YES : constTranslate.NO}</span>
            </div>
          </div>
        </div>
      </div>
      <div className="contactBottom">
        <form className="contactForm" onSubmit={onSaveContact}>
          <div className="contactFormLeft">
            <label>{constTranslate.COMPANY}</label>
            <OptionsSelect
              options={companies}
              cb={handleSelectChange}
              accessor={JsConst.COMPANY}
              name={constContact.COMPANY}
              selectedValue={contact[constContact.COMPANY] || ''}
            />
            <label>{constTranslate.DESCRIPTION}</label>
            <input type="text" defaultValue={contact[JsConst.DESC]} name={[JsConst.DESC]} onChange={handleChange} />
            <label>{constTranslate.PRICE}</label>
            <input type="text" defaultValue={contact[JsConst.PRICE]} name={[JsConst.PRICE]} onChange={handleChange} />
            <label>{constTranslate.IN_STOCK}</label>
            <OptionsSelect
              options={[{ _id: constTranslate.YES, value: 'true' }, { _id: constTranslate.NO, value: 'false' }]}
              cb={handleSelectChange}
              accessor={JsConst._ID}
              name={JsConst.IN_STOCK}
              value={'value'}
              selectedValue={contact[constContact.IN_STOCK] ? 'true' : (!contact[constContact.IN_STOCK] ? 'false' : '')}
            />

            <label>{constTranslate.JOB_TITLE}</label>
            <OptionsSelect
              options={jobTitles}
              cb={handleSelectChange}
              accessor={JsConst.TITLE}
              name={constContact.JOB_TITLE}
              selectedValue={contact[constContact.JOB_TITLE] || ''} />
          </div>

          <div className="contactFormLeft">
            <label>{constTranslate.NAME}</label>
            <input type="text" defaultValue={contact[JsConst.NAME]} name={JsConst.NAME} onChange={handleChange} />
            <label>{constTranslate.FAMILY_NAME}</label>
            <input type="text" defaultValue={contact[JsConst.FAMILY_NAME]} name={JsConst.FAMILY_NAME} onChange={handleChange} />
            <label>{constTranslate.COUNTRY}</label>
            <OptionsSelect
              options={territories}
              cb={handleSelectChange}
              accessor={'territoryName'}
              name={constContact.COUNTRY}
              value={"country"}
              selectedValue={contact[constContact.COUNTRY] || ''} />

            <label>{constTranslate.AGENT}</label>
            <OptionsSelect
              options={getAgetsUsers()}
              cb={handleSelectChange}
              accessor={constUser.FULLNAME}
              name={constContact.AGENT}
              value={"fullname"}
              selectedValue={contact[constContact.AGENT]?.fullname || ''} />

            <label>{constTranslate.CATEGORY}</label>
            <OptionsSelect
              options={categories}
              cb={handleSelectChange}
              accessor={JsConst.TITLE}
              name={constContact.CATEGORY}
              selectedValue={contact[constContact.CATEGORY] || ''} />
          </div>

          <div className="contactFormLeft">
            <label>{constTranslate.EMAIL}</label>
            <input name={JsConst.EMAIL} type="text" defaultValue={contact[JsConst.EMAIL]} onChange={handleChange} />

            <label>{constTranslate.PHONE}</label>
            <input name={JsConst.PHONE} type="text" defaultValue={contact[JsConst.PHONE]} onChange={handleChange} />

            <label>{constTranslate.LINKEDIN_LINK}</label>
            <input name={JsConst.LINKEDIN_LINK} defaultValue={contact[JsConst.LINKEDIN_LINK]} type="text" onChange={handleChange} />
          </div>

          <div className="contactFormRight">
            <FileUploader cb={updateContactImage} name={JsConst.IMG} />
            <button className="contactButton">{constTranslate.UPDATE}</button>
          </div>
        </form>
      </div>
    </div>
  );
}
