import { contactService } from '../../services/contact.service'
import {
    addContactFailure,
    addContactStart,
    addContactSuccess,
    addContactsFailure,
    addContactsStart,
    addContactsSuccess,
    deleteContactStart,
    deleteContactSuccess,
    getContactFailure,
    getContactStart,
    getContactSuccess,
    updateContactFailure,
    updateContactStart,
    updateContactSuccess
} from "./contactRedux";

export const getContacts = async (dispatch, filterBy) => {
    dispatch(getContactStart());
    try {
        const contacts = await contactService.query(filterBy)
        dispatch(getContactSuccess(contacts));
    } catch (err) {
        console.log('Error => ', err)
        dispatch(getContactFailure());
    }
}

export const deleteContact = async (dispatch, id, currentUser) => {
    dispatch(deleteContactStart());
    try {
        await contactService.removeContact(id, currentUser)
        dispatch(deleteContactSuccess(id))
    } catch (err) {
        throw err
    }
};

export const updateContact = async (dispatch, contact, currentUser) => {
    dispatch(updateContactStart());
    try {
        const res = await contactService.updateContact(contact, currentUser)
        dispatch(updateContactSuccess(res))
    } catch (err) {
        console.log('Error => ', err)
        dispatch(updateContactFailure());
    }
};

export const createContact = async (dispatch, contact) => {
    dispatch(addContactStart());
    try {
        const savedContact = await contactService.createContact(contact)
        dispatch(addContactSuccess(savedContact))
    } catch (err) {
        dispatch(addContactFailure());
    }
};

export const createContacts = async (dispatch, contact) => {
    dispatch(addContactsStart());
    try {
        const savedContacts = await contactService.createContacts(contact)
        dispatch(addContactsSuccess(savedContacts))
    } catch (err) {
        dispatch(addContactsFailure());
    }
};