import { UserWaitlistEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const userWaitlistService = {
    query,
    remove,
    update,
    create,
}

export async function query(filterBy = {}) {
    try {
        const res = await httpService.post(UserWaitlistEP.get)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get waiting list')
    } catch (err) {
        throw err
    }
}

async function remove(request) {
    try {
        const content = { user: request }
        return await httpService.post(`${UserWaitlistEP.remove}/${request._id}`, content)
    } catch (err) {
        throw err
    }
}

async function update(request, status) {
    try {
        if (request._id) {
            const content = { user: request, status }
            const res = await httpService.post(`${UserWaitlistEP.update}/${request._id}`, content)
            if (res.status === 'ok') return res.content
            else throw new Error("Couldn't update user request")
        }
    } catch (err) {
        throw err
    }
}

async function create(category) {
    try {
        const content = { category }
        const res = await httpService.post(`${UserWaitlistEP.create}`, content)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't create user request")
    } catch (err) {
        throw err
    }
}