import { useCallback, useEffect } from "react"
import { DUMMY_USER_URL } from "../../../config/appConfig"
import { utilService } from "../../../services/util.service"
import { showErrorMsg } from "../../../services/eventBusService"
import { UserMsg } from "../../../constant/userMessage"
import { useForm } from "react-hook-form"
import { useSelector } from "react-redux"
import { supportChatService } from "../../../services/supportChat.service"
import { useState } from "react"

export const Chat = ({ chat }) => {
    const { currentUser } = useSelector(state => state.user)
    const [messages, setMessages] = useState([])
    const { register, handleSubmit, reset } = useForm();

    useEffect(() => {
        if (!chat) return
        setMessages(chat.messages)
    }, [chat])

    const onSubmit = useCallback(async ({ text }) => {
        try {
            const messageResponse = await supportChatService.send({ recieverId: chat.chatId, currentUserId: currentUser._id, text })
            if (messageResponse) {
                setMessages(prevState => ([...prevState, messageResponse]))
            }
            reset()
        } catch (error) {
            showErrorMsg(UserMsg.CANNOT_LOAD_CHAT)
        }
    }, [chat])

    return (
        <section className="chatbox-container">
            {chat &&
                messages.map(message => {
                    return (
                        <div key={message._id} className={`msg-row${message.isUserSender ? '' : ' admin-msg'}`}>
                            <div className="msg-text">
                                <h2>{message.senderName}</h2>
                                <p className="msg-time">{utilService.formatTimestamp(new Date(message.createdAt))}</p>
                                <p>{message.content}</p>
                            </div>
                            <img src={DUMMY_USER_URL} className="msg-img" alt="msg-img" />
                        </div>
                    )
                })
            }
            <form onSubmit={handleSubmit(onSubmit)}>
                <input name="text" type="text" placeholder="Type your messages here" {...register("text", { required: true })} />
            </form>
        </section>
    )
}