import { Modal } from "../Modal";
import { constContact } from "../../model/contact/constContact";

import CategoryIcon from '@mui/icons-material/Category';
import WorkIcon from '@mui/icons-material/Work';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';
import DescriptionIcon from '@mui/icons-material/Description';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import PendingIcon from '@mui/icons-material/Pending';
import { utilService } from "../../services/util.service";
import styled from "styled-components";
import { useState } from "react";
import { DUMMY_USER_URL } from "../../config/appConfig";
import { constTranslate } from "../../constant/translate";
import { LoadingButton } from "../Core/LoadingButton";

const ModalWrapper = styled.div`
  display: flex;
  gap: 20px;
  flex-direction: column;
`

const ModalHeader = styled.div`
  display: flex;
  gap: 20px;
`;

const ImgContainer = styled.div`
  width: 90px;
  height: 90px;

  & img {
    border-radius: 50%;
  }
`;

const ModalTitleContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 5px;
`;

const ModalTitle = styled.h2`
  font-weight: 400;
`

const ModalSubTitleContainer = styled.div`
  display: flex;
  gap: 10px;
`

const MarkGreen = styled.span`
  padding: 3px 5px;
  border-radius: 3px;
  background-color: #34c7ba;
  color: #fafafa;
  font-size: 0.8rem;
`

const MarkOrange = styled.span`
  padding: 3px 5px;
  border-radius: 3px;
  background-color: #fab96d;
  color: #fafafa;
  font-size: 0.8rem;
`

const CardContainer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;

  ${props => props.theme.breakpoints.small} {
    grid-template-columns: 1fr;
  }
`

const Card = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 1px solid #e7e7e9;
  padding: 10px;
  border-radius: 5px;
  transition: .1s;

  &:hover {
    box-shadow: -1px 10px 20px -15px rgba(0,0,0,0.75);
  }
`

const CardTitle = styled.h4`
  display: flex;
  align-items: center;
  gap: 5px;
`

const CardContent = styled.p`
  color: #676879;
  font-size: 0.8rem;
`

const CardContentList = styled.div`
`


export const ContactInfoModal = ({ setModalProps, modalProps }) => {
  const [contact] = useState(modalProps.contact)

  return (
    <Modal setModalProps={setModalProps} style={{ width: '80%', maxWidth: '700px' }}>
      <ModalWrapper>
        <ModalHeader>
          <ImgContainer>
            <img src={contact?.img?.url || DUMMY_USER_URL} alt="user" />
          </ImgContainer>
          <ModalTitleContainer>
            <ModalTitle>

              {contact.name} {contact.familyName}
            </ModalTitle>
            <ModalSubTitleContainer>
              <MarkOrange>{modalProps.isApproved || contact[constContact.IN_STOCK] ? constTranslate.IN_STOCK : constTranslate.PENDING}</MarkOrange>
              <MarkGreen>{contact[constContact.COMPANY]}</MarkGreen>
            </ModalSubTitleContainer>
          </ModalTitleContainer>
        </ModalHeader>

        {!contact[constContact.IN_STOCK] && !modalProps.toApprove ?
          <CardContainer>
            {contact[constContact.CATEGORY] && <Card>
              <CardTitle>
                <PendingIcon />
                Date of introduction of the requirement
              </CardTitle>
              <CardContent>{utilService.getFormattedDate(new Date(modalProps.createdAt))}</CardContent>
            </Card>}
          </CardContainer>

          :

          <CardContainer>
            {contact[constContact.CATEGORY] && <Card>
              <CardTitle>
                <CategoryIcon />
                {constTranslate.CATEGORY}
              </CardTitle>
              <CardContent>{contact[constContact.CATEGORY]}</CardContent>
            </Card>}

            {contact[constContact.JOB_TITLE] && <Card>
              <CardTitle>
                <WorkIcon />
                {constTranslate.JOB_TITLE}
              </CardTitle>
              <CardContent>{contact[constContact.JOB_TITLE]}</CardContent>
            </Card>}

            {contact[constContact.PHONE] && <Card>
              <CardTitle>
                <PhoneAndroidIcon />
                {constTranslate.MOBILE}
              </CardTitle>
              <CardContentList>
                <CardContent>{contact[constContact.PHONE]}</CardContent>
              </CardContentList>
            </Card>}

            {contact[constContact.EMAILS] && contact[constContact.EMAILS]?.length > 0 && <Card>
              <CardTitle>
                <AlternateEmailIcon />
                {constTranslate.EMAIL}
              </CardTitle>
              <CardContentList>
                {contact[constContact.EMAILS].map((email, idx) => <CardContent key={idx}>{email}</CardContent>)}
              </CardContentList>
            </Card>}

            {contact[constContact.DESCRIPTION] && <Card>
              <CardTitle>
                <DescriptionIcon />
                {constTranslate.DESCRIPTION}
              </CardTitle>
              <CardContent>{contact[constContact.DESCRIPTION]}</CardContent>
            </Card>}

            <Card>
              <CardTitle>
                <LinkedInIcon />
                {constTranslate.LINKEDIN_LINK}
              </CardTitle>
              <CardContent>{contact[constContact.LINKEDIN_LINKS]}</CardContent>
            </Card>
          </CardContainer>
        }
        {modalProps.toApprove && <div className="btn-container">
          {!modalProps.isApproved && <LoadingButton onClick={modalProps.saveCb} isLoading={modalProps.approvedIsLoading} >
            {constTranslate.TO_APPROVE}
          </LoadingButton>}
        </div>}
      </ModalWrapper>
    </Modal>
  )
}