import { useEffect, useState } from "react"
import { userWaitlistService } from "../../../services/userWaitlist.service"
import { DataGrid } from "@mui/x-data-grid"
import { JsConst } from "../../../constant/JsConst"
import { constTranslate } from "../../../constant/translate"
import { utilService } from "../../../services/util.service"
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService";
import { DeleteOutline } from "@material-ui/icons"
import { WaitlistStatus } from "../../../constant/waitlistStatus"
import { AdminUserMsg } from "../../../constant/userMessage"


export const PendingUserList = () => {
    const [pendingUsers, setPendingUsers] = useState([])

    useEffect(() => {
        loadPendingUsers()
    }, [])

    const loadPendingUsers = async () => {
        try {
            const users = await userWaitlistService.query()
            setPendingUsers(users)
        } catch (err) {

        }
    }

    const onChangeStatus = async (request, status) => {
        try {
            const res = await userWaitlistService.update(request, status)
            updatePendingUser(res)
            if (status === WaitlistStatus.APPROVED) showSuccessMsg(AdminUserMsg.USER_APPROVED_SUCCESSFULLY)
            else showSuccessMsg(AdminUserMsg.USER_REJECTED_SUCCESSFULLY)
        } catch (err) {
            showErrorMsg(AdminUserMsg.CANNOT_APPROVE_USER)
        }
    }

    const onDeletePendingUser = async (request) => {
        try {
            await userWaitlistService.remove(request)
            setPendingUsers(prevState => prevState.filter(currRequest => currRequest._id !== request._id))
            showSuccessMsg(AdminUserMsg.USER_DELETED_SUCCESSFULLY)
        } catch (error) {
            showErrorMsg(AdminUserMsg.CANNOT_DELETE_USER)
        }
    }

    const updatePendingUser = (pendingUser) => {
        const res = utilService.updateObjectById(pendingUsers, pendingUser._id, pendingUser)
        setPendingUsers(res)
    }

    const columns = [
        {
            field: JsConst.FULLNAME,
            headerName: constTranslate.FULLNAME,
            width: 120
        },
        {
            field: JsConst.USERNAME,
            headerName: constTranslate.USERNAME,
            width: 120,
        },
        {
            field: JsConst.EMAIL,
            headerName: constTranslate.EMAIL,
            width: 250
        },
        {
            field: JsConst.APPROVE_STATUS,
            headerName: constTranslate.APPROVE_STATUS,
            width: 160,
        },
        {
            field: JsConst.ACTION,
            headerName: constTranslate.ACTION,
            width: 300,
            renderCell: (params) => {
                return (
                    <div className="btn-container">
                        <button className="btn primary-btn" onClick={() => onChangeStatus(params.row, WaitlistStatus.APPROVED)}>
                            {constTranslate.APPROVE}
                        </button>
                        <button
                            className="btn secondary-btn" onClick={() => onChangeStatus(params.row, WaitlistStatus.REJECTED)}>
                            {constTranslate.REJECT}
                        </button>
                        <DeleteOutline titleAccess="Remove" className="userListDelete" onClick={() => onDeletePendingUser(params.row)} />
                    </div>
                )
            },
        },
    ];


    return (
        <div className="flex-4">
            <DataGrid
                rows={pendingUsers}
                columns={columns}
                pageSize={10}
                getRowId={(row) => row._id}
            />
        </div>
    )
}
