import "./ContactRequest.css"
import { DataGrid } from "@mui/x-data-grid"
import { useEffect } from "react"
import { useState } from "react"
import { contactRequestService } from "../../../services/contactRequest.service"
import { constTranslate } from "../../../constant/translate"
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService"
import { useSelector } from "react-redux"
import { AddContactModal } from "../../../components/Modal/AddContactModal"
import { Modal } from "../../../components/Modal"
import { getColumns } from "./ContactRequestAdminColumns"
import { AdminUserMsg, UserMsg } from "../../../constant/userMessage"

export default function ContactRequestAdmin() {
    const { currentUser } = useSelector(state => state.user)
    const [requests, setRequests] = useState([])
    const [modalProps, setModalProps] = useState(null)

    useEffect(() => {
        loadRequests()
    }, [])

    const loadRequests = async () => {
        try {
            const requests = await contactRequestService.query()
            if (requests?.length) setRequests(requests.reverse())
        } catch {
            setRequests([])
        }
    }

    const removeRequest = async (requestId) => {
        const res = window.confirm(AdminUserMsg.ADMIN_DELETE_MSG_ASK)
        if (!res) return
        try {
            await contactRequestService.removeContactRequest(requestId)
            setRequests((prevState) => {
                return prevState.filter((item) => item._id !== requestId)
            })
            showSuccessMsg(AdminUserMsg.SUCCESS_REMOVE_MSG)
        } catch (err) {
            showErrorMsg(AdminUserMsg.ERROR_REMOVE_MSG)
        }
    }

    const isApprovedContact = async (contactRequest) => {
        if (contactRequest.isApproved) {
            return showSuccessMsg(AdminUserMsg.CONTACT_ALREADY_APPROVED)
        }

        try {
            const res = await contactRequestService.updateContactRequest(contactRequest)
            if (res) updateRequest(res)
            showSuccessMsg(AdminUserMsg.CONTACT_APPROVED)
        } catch (err) {
            showErrorMsg(AdminUserMsg.CANNOT_APPROVE_CONTACT)
        }
        setModalProps(null)
    }

    const updateRequest = (contactRequest) => {
        const updatedRequests = requests.map(request => request._id === contactRequest._id ? { ...contactRequest } : request)
        setRequests([...updatedRequests])
    }

    const onSubmit = async (form) => {
        const res = window.confirm(UserMsg.PROMPT_CONFIRM_MSG)
        if (!res) return
        const request = requests.find(request => request.contact._id === form._id)
        request.contact = { ...form }
        isApprovedContact(request)
    }

    return (
        <div className="contactList">
            <DataGrid
                rows={requests}
                disableSelectionOnClick
                columns={getColumns(setModalProps, removeRequest)}
                getRowId={(row) => row._id}
                pageSize={10}
            />
            {
                modalProps && <Modal setModalProps={setModalProps} style={{ width: '80%', maxWidth: '700px' }} isClosingOutside={false}>
                    <AddContactModal
                        currentUser={currentUser}
                        modalProps={modalProps}
                        submitCb={onSubmit}
                        btnText={constTranslate.APPROVE}
                        title={`Approve this contact`} />
                </Modal>
            }
        </div>
    )
}
