import { DUMMY_USER_URL } from "../../config/appConfig";
import { utilService } from "../../services/util.service";

export const creditAdminColumns = [
  {
    field: "userInfo",
    headerName: "Fullname",
    width: 200,
    renderCell: (params) => {
      return (
        <div className="flex align-center">
          <img className="userListImg" src={params.row.userInfo?.imgUrl?.url || DUMMY_USER_URL} alt="user" />
          {params.row.userInfo.fullname}
        </div>
      );
    },
  },
  { field: "type", headerName: "Transaction Type", width: 130 },
  { field: "creditName", headerName: "Package Name", width: 130 },
  { field: "creditQuantity", headerName: "Credit Quantity", width: 130 },
  { field: "packagePrice", headerName: "Package Price", width: 130 },
  {
    field: "createdAt",
    headerName: "Transaction Date",
    width: 200,
    renderCell: (params) => {
      return (
        <div>
          {utilService.getFormattedDate(new Date(params.row.createdAt))}
        </div>
      );
    },
  },
];