import styled from "styled-components";
import { LoadingButton } from "../Core/LoadingButton";
import { constTranslate } from "../../constant/translate";
import { mobile } from "../../responsive";
import { Link } from "react-router-dom";
import { ConstRoutes } from "../../constant/routes";

const Wrapper = styled.div`
  width: 25%;
  padding: 20px;
  background-color: white;
  ${mobile({ width: "75%" })}
`;

const Title = styled.h1`
  font-size: 24px;
  font-weight: 300;
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
`;

const Input = styled.input`
  flex: 1;
  min-width: 40%;
  margin: 10px 0;
  padding: 10px;
`;

// const Link = styled.a`
//   margin: 5px 0px;
//   font-size: 12px;
//   text-decoration: underline;
//   cursor: pointer;
// `;

const Span = styled.span`
  margin: 5px 0px;
  font-size: 12px;
  text-decoration: underline;
  cursor: pointer;
`;

const Error = styled.span`
  color: red;
`;

export const LoginComponent = ({ onSubmit, setUsername, setPassword, isLoading, error, setType }) => {
  return (
    <Wrapper>
      <Title>SIGN IN</Title>
      <Form onSubmit={onSubmit}>
        <Input
          placeholder="username"
          onChange={(e) => setUsername(e.target.value)}
        />
        <Input
          placeholder="password"
          type="password"
          onChange={(e) => setPassword(e.target.value)}
        />
        <LoadingButton isLoading={isLoading}>
          {constTranslate.LOGIN}
        </LoadingButton>
        {error.msg && <Error>Something went wrong...</Error>}
        <Span onClick={() => setType('otp')}>FORGOT PASSWORD?</Span>
        <Link to={`${ConstRoutes.REGISTER}`}><Span>CREATE A NEW ACCOUNT</Span></Link>
      </Form>
    </Wrapper>
  )
}