import styled from "styled-components";
import { constTranslate } from "../../constant/translate";
import { Notification } from "./Notification";
import { constNotification } from "../../model/notification/constNotification";
import { useEffect, useState } from "react";
import { constUser } from "../../model/user/constUser";
import { useDispatch } from "react-redux";
import { updateNotification } from "../../redux/user/apiCalls";

const Container = styled.div`
    width: 500px;
`;

const Header = styled.div`
    display: flex;
    justify-content: space-between;
`;

const Content = styled.div`
    margin-top: 5px;
    height: 500px;
    overflow-y: auto;
`

const Footer = styled.div`
    margin-top: 20px;
`;

const Title = styled.h3`

`
const Bolded = styled.span`
    color: #3175e3;
    font-weight: 600;
    cursor: pointer;
`


export const Notifications = ({ modalProps }) => {
    const { currentUser } = modalProps
    const [showAll, setShowAll] = useState(false)
    const dispatch = useDispatch()

    useEffect(() => {
        const unreadNote = currentUser.notifications.find(note => !note[constNotification.IS_READ])
        if (unreadNote) {
            const updatedUser = updateNotifications()
            saveUpdatedNotifications(updatedUser)
        }
    }, [])

    const updateNotifications = () => {
        let updatedUser = { ...currentUser }
        let updatedNotifications = [...currentUser[constUser.NOTIFICATIONS]]
        updatedNotifications.forEach((note, index) => {
            let updatedNote = { ...note, isRead: true }
            updatedNotifications[index] = updatedNote
        })
        updatedUser[constUser.NOTIFICATIONS] = updatedNotifications
        return updatedUser
    }

    const saveUpdatedNotifications = async (updatedUser) => {
        try {
            await updateNotification(dispatch, updatedUser)
        } catch (err) {
            console.log(err)
        }
    }

    const getFilteredNotifications = () => {
        if (!showAll) {
            return currentUser.notifications.filter(notification => !notification[constNotification.IS_READ])
        } else {
            return currentUser.notifications
        }
    }

    return (
        <Container>
            <Header>
                <Title>{constTranslate.NOTIFICATIONS}</Title>
            </Header>

            <Content>
                {
                    getFilteredNotifications().length ?
                        getFilteredNotifications().map(notification =>
                            <Notification key={notification[constNotification._ID]} notification={notification} />)
                        :
                        <div>No Updates For Now</div>
                }
            </Content>

            <Footer>
                <Bolded onClick={() => setShowAll(!showAll)}>{
                    !showAll ? constTranslate.VIEW_ALL_NOTIFICATIONS : constTranslate.VIEW_ONLY_UNREAD
                }</Bolded>
            </Footer>
        </Container>
    )
}