import { useDispatch, useSelector } from "react-redux"
import { OptionsSelect } from "../../../components/OptionsSelect"
import { constTranslate } from "../../../constant/translate"
import { JsConst } from "../../../constant/JsConst"
import { useState } from "react"
import { MultiSelect } from "../../../components/MultiSelect"
import { FileUploader } from "../../../components/FileUploader"
import { createContacts } from "../../../redux/contact/apiCalls"
import { territoryConst } from "../../../constant/Model/territoryConst"
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService"
import { AdminUserMsg } from "../../../constant/userMessage"
import { TextField } from "@mui/material"
import { useNavigate } from "react-router-dom"
import { ConstRoutes } from "../../../constant/routes"
import { Modal } from "../../../components/Modal"
import { AddNewCategoryModal } from "../../../components/Modal/AddNewCategoryModal"



const NewCompanyContacts = () => {
    const { categories, jobTitles, companies, territories } = useSelector(state => state.categoryManager)
    const [selectedCat, setSelectedCat] = useState(null)
    const [modalProps, setModalProps] = useState(null)
    const [imgUrl, setImgUrl] = useState(null)
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const handleCatSelect = (name, value) => {
        if (name === JsConst.CATEGORY) {
            setSelectedCat(value)
        }
    }

    const getCompanyByCategory = (companies) => {
        if (companies && selectedCat) {
            return companies.filter(company => company[JsConst.CATEGORY] === selectedCat.toLowerCase())
        } else return companies
    }

    const onSubmit = async (event) => {
        event.preventDefault()
        if (!window.confirm('Are you sure?')) return
        try {
            const formObj = Object.fromEntries(new FormData(event.target))
            if (formObj.jobTitle) formObj.jobTitle = formObj.jobTitle.split(',')
            if (formObj.inStock) formObj.inStock = formObj.inStock === 'Yes' ? true : false
            formObj.img = imgUrl
            await createContacts(dispatch, formObj)
            showSuccessMsg(AdminUserMsg.CONTACTS_CREATED)
            navigate(ConstRoutes.ADMIN_CONTACTS)
        } catch (error) {
            showErrorMsg(AdminUserMsg.ERROR_CREATE_CONTACTS)
        }
    }

    return (
        <div className="flex-4">
            <h1 className="addContactTitle">{constTranslate.NEW_CONPANY_CONTACTS}</h1>

            <form className="addContactForm" onSubmit={onSubmit}>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.JOB_TITLES}</label>
                    </div>
                    <MultiSelect
                        options={jobTitles}
                        accessor={JsConst.TITLE}
                        name={JsConst.JOBTITLE}
                        isRequired={true} />
                </div>

                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.DESCRIPTION}</label>
                    </div>
                    <TextField
                        id="outlined-multiline-static"
                        multiline
                        rows={4}
                        name={JsConst.DESC}
                        placeholder={constTranslate.DESCRIPTION}
                        required={true} />
                </div>

                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.CATEGORIES}</label>
                    </div>
                    <OptionsSelect
                        options={categories}
                        cb={handleCatSelect}
                        accessor={JsConst.TITLE}
                        name={JsConst.CATEGORY}
                        isRequired={true} />
                </div>

                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.COMPANY}</label>
                        <span onClick={() => setModalProps({ type: JsConst.COMPANIES, idx: 3 })}>{constTranslate.ADD_NEW}</span>
                    </div>
                    <OptionsSelect
                        options={getCompanyByCategory(companies)}
                        accessor={JsConst.COMPANY}
                        name={JsConst.COMPANY}
                        isRequired={true} />
                </div>

                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.COUNTRY}</label>
                    </div>
                    <OptionsSelect
                        options={territories}
                        accessor={territoryConst.TERRITORY_NAME}
                        name={JsConst.COUNTRY}
                        isRequired={true} />
                </div>


                <div className="addContactItem">
                    <label>{constTranslate.PRICE}</label>
                    <input name={JsConst.PRICE} type="number" placeholder="100" required={true} />
                </div>

                <div className="addContactItem">
                    <label>{constTranslate.IN_STOCK}</label>
                    <OptionsSelect
                        options={[{ _id: constTranslate.YES }, { _id: constTranslate.NO }]}
                        accessor={JsConst._ID}
                        name={JsConst.IN_STOCK}
                        isRequired={true} />
                </div>

                <div className="addContactItem">
                    <label>{constTranslate.IMAGE}</label>
                    <FileUploader cb={(res) => setImgUrl(res)} />
                </div>

                <div className="add-contact-button--container">
                    <button className="addContactButton" type="submit">{constTranslate.CREATE}</button>
                </div>
            </form>
            {
                modalProps && <Modal categories={categories} setModalProps={setModalProps}>
                    <AddNewCategoryModal {...modalProps} categories={categories} setModalProps={setModalProps} />
                </Modal>
            }
        </div>
    )
}

export default NewCompanyContacts