import { DUMMY_USER_URL } from "../../../config/appConfig";
import { JsConst } from "../../../constant/JsConst";
import { constTranslate } from "../../../constant/translate";
import { constContact } from "../../../model/contact/constContact";
import { utilService } from "../../../services/util.service";

export function getColumns(setModalProps, removeRequest) {
    return [
        {
            field: "agentName",
            headerName: "Agent Name",
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        <img className="contactListImg" src={params.row?.contact?.agent?.imgUrl?.url || DUMMY_USER_URL} alt="" />
                        {params.row?.contact?.agent?.fullname || constTranslate.UNKNOWN}
                    </div>
                );
            },
        },
        { field: JsConst.IS_APPROVED, headerName: constTranslate.IS_APPROVED, width: 120 },
        {
            field: JsConst.CONTACT_NAME,
            headerName: constTranslate.CONTACT_NAME,
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {`${params.row.contact[constContact.NAME]} ${params.row.contact[constContact.FAMILY_NAME]}`}
                    </div>
                )
            }
        },
        {
            field: JsConst.CONTACT_COMPANY,
            headerName: constTranslate.CONTACT_COMPANY,
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {`${params.row.contact[constContact.COMPANY]}`}
                    </div>
                )
            }
        },
        {
            field: JsConst.CONTACT_TITLE,
            headerName: constTranslate.CONTACT_TITLE,
            width: 120,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {`${params.row.contact[constContact.JOB_TITLE]}`}
                    </div>
                )
            }
        },
        {
            field: JsConst.CREATED_AT,
            headerName: constTranslate.CREATED_AT,
            width: 120,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {utilService.getFormattedDate(new Date(params.row[JsConst.CREATED_AT]))}
                    </div>
                )
            }
        },
        {
            field: JsConst.UPDATED_AT,
            headerName: constTranslate.APPROVED_AT,
            width: 120,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {params.row[JsConst.UPDATED_AT] ? utilService.getFormattedDate(new Date(params.row[JsConst.UPDATED_AT])) : constTranslate.NO_UPDATE}
                    </div>
                )
            }
        },
        {
            field: "action",
            headerName: "Action",
            width: 250,
            renderCell: (params) => {
                return (
                    <>
                        <button
                            className="contactListEdit"
                            onClick={() => setModalProps({ ...params.row.contact })}>
                            {constTranslate.CONTACT_DETAILS}
                        </button>
                        <button
                            className="btn secondary-btn"
                            onClick={() => removeRequest(params.row._id)}>
                            {constTranslate.REMOVE}
                        </button>
                    </>
                );
            },
        },
    ]
} 