import { DataGrid } from "@mui/x-data-grid";
import { constTranslate } from "../../../constant/translate";
import { Tooltip } from "@mui/material";
import { utilService } from "../../../services/util.service";
import { DUMMY_USER_URL } from "../../../config/appConfig";

export const ContactsFeedback = ({ feedbacks }) => {
    const columns = [
        {
            field: "company",
            headerName: constTranslate.COMPANY,
            width: 150,
            renderCell: (params) => {
                return (
                    <>
                        <img className="contactListImg" src={params.row?.contact?.img?.url || DUMMY_USER_URL} alt="" />
                        <p>{params.row.contact.company}</p>
                    </>
                )
            },
        },
        {
            field: "category",
            headerName: "Category",
            width: 150,
            renderCell: (params) => {
                return <p>{params.row.contact.category}</p>
            },
        },
        {
            field: "jobTitle",
            headerName: constTranslate.JOB_TITLE,
            width: 150,
            renderCell: (params) => {
                return <p>{params.row.contact.jobTitle}</p>
            },
        },
        {
            field: "createdAt",
            headerName: "Created At",
            width: 150,
            renderCell: (params) => {
                return <p>{utilService.getFormattedDate(new Date(params.row.createdAt))}</p>
            },
        },
        {
            field: "comment",
            headerName: "Messages",
            width: 150,
            renderCell: (params) => {
                return (
                    <Tooltip title={`${params.row.comment}`}>
                        <p className="text single-line">
                            {params.row.comment}
                        </p>
                    </Tooltip>
                );
            },
        },
        {
            field: "rating",
            headerName: "Rating",
            width: 90,
            renderCell: (params) => {
                return <p>{params.row.rating}</p>
            },
        },
    ]


    return (
        <div style={{ height: "400px" }}>
            <h3 className="mb-10">{constTranslate.CONTACTS_FEEDBACK}</h3>
            <DataGrid
                rows={feedbacks}
                disableSelectionOnClick
                columns={columns}
                getRowId={(row) => row._id}
                pageSize={10}
                style={{ height: "100%" }}
            />
        </div>
    )
}