import { Contact } from "../contact/Contact"


export class ContactRequest {
    contact
    isApproved
    status
    createdAt
    updatedAt

    constructor(contactRequest) {
        this.contact = new Contact(contactRequest)
        this.isApproved = false
        this.status = 'pending'
        this.createdAt = new Date()
        this.updatedAt = null
    }
}