import { FeedbacksEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const feedbackService = {
    getUserFeedback,
}

async function getUserFeedback() {
    try {
        const res = await httpService.get(`${FeedbacksEP.getUserFeedback}`)
        if (res.status === 'ok') return res.content
        else throw new Error(res.message)
    } catch (err) {
        throw err
    }
}
