import { DataGrid } from "@mui/x-data-grid"
import { constTranslate } from "../../constant/translate"
import { constContact } from "../../model/contact/constContact"
import { ComponentLoader } from "../Loader/ComponentLoader"
import { contactService } from "../../services/contact.service"
import { showErrorMsg } from "../../services/eventBusService"
import { useEffect, useState } from "react"

export const ContactSaleModal = ({ modalProps }) => {
    const [isLoading, setIsLoading] = useState(false)
    const [contacts, setContacts] = useState([])

    useEffect(() => {
        loadUserContacts()
    }, [])

    const loadUserContacts = async () => {
        setIsLoading(true)
        try {
            const res = await contactService.queryByUserId(modalProps.userId)
            setContacts(res)
        } catch (err) {
            showErrorMsg('Cannot get your contacts')
        } finally {
            setIsLoading(false)
        }
    }

    const columns = [
        { field: constContact.COMPANY, headerName: constTranslate.COMPANY, width: 250 },
        { field: constContact.JOB_TITLE, headerName: constTranslate.JOB_TITLE, width: 90 },
        {
            field: constContact.TRANSACTION_HISTORY,
            headerName: constTranslate.QUANTITY,
            width: 90,
            renderCell: (params) => {
                return (
                    <>
                        {params.row[constContact.TRANSACTION_HISTORY]?.length || 0}
                    </>
                )
            }
        },
    ];

    return (
        <div className="contact-transaction-modal">
            <h3>{constTranslate.CONTACT_SUMMARY}</h3>
            {!isLoading && <DataGrid
                rows={contacts}
                disableSelectionOnClick
                columns={columns}
                getRowId={(row) => row._id}
                pageSize={10}
            />}
            <ComponentLoader isLoading={isLoading} />
        </div>
    )
}