import styled from "styled-components";
import { constNotification } from "../../model/notification/constNotification";
import { utilService } from "../../services/util.service";

const Container = styled.div`
`;

const Info = styled.div`
    padding: 20px 0;
    border-bottom: 1px solid #e1e4e5;
    display: flex;
    gap: 10px;
    transition: .2s;
    
    &:hover {
        border-radius: 5px;
        background-color: #f6f6f6;
    }
`

const NotificationTitle = styled.p`
    font-weight: 600;
`

const NoteTime = styled.span`
    color: #676879;
    font-weight: 600;
    font-size: 14px;
    flex: 1;
`

const UnreadContainer = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10px;
`

const Unread = styled.div`
    background-color: #1976d2;
    height: 10px;
    width: 10px;
    border-radius: 50%;
`

const NoteContainer = styled.div`
    display: flex;
    flex-direction: column;
    gap: 10px;
`

export const Notification = ({ notification }) => {

    return (
        <Container>
            <Info>
                <UnreadContainer>
                    {!notification[constNotification.IS_READ] && <Unread />}
                </UnreadContainer>

                <NoteContainer>

                    <NotificationTitle>
                        {notification[constNotification.MESSAGE]}
                    </NotificationTitle>

                    <NoteTime>
                        {utilService.getFullDateFormat(new Date(notification[constNotification.CREATED_AT]))}
                    </NoteTime>
                </NoteContainer>
            </Info>
        </Container>
    )
}