import { JsConst } from '../constant/JsConst'
import { contactTransType } from '../constant/contactTransType'
import { constSocket } from '../constant/socket'
import { User } from '../model/user/User'
import { httpService } from './http.service'
import { socketService } from './socket.service'

const STORAGE_KEY_LOGGEDIN_USER = 'loggedinUser'
export const userService = {
    login,
    logout,
    signup,
    createUser,
    updateUser,
    getUsersStats,
    isAdmin,
    getLoggedinUser,
    saveLocalUser,
    clearLocalUser,
    getUsers,
    getById,
    remove,
    update,
    isAgentUser,
    getAgentUsers,
    getContactPurchaseType,
    getContactSaleType,
    changeUserPassByEmail,
}

async function getUsers(filterBy = {}) {
    try {
        const res = await httpService.post(`users`, filterBy)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot get users')
    } catch (err) {
        throw new Error('Cannot get users')
    }
}

async function getUsersStats(currentUser) {
    try {
        return await httpService.post(`users/stats`, currentUser)
    } catch (err) {
        console.log(err)
    }
}

async function getById(userId) {
    try {
        const user = await httpService.post(`users/find/${userId}`, { userId })
        return user
    } catch (err) {
        throw new Error('Cannot find user')
    }
}

async function remove(userId, currentUser) {
    try {
        const content = { userId, user: currentUser }
        const res = await httpService.post(`users/remove/${userId}`, content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot delete user')
    } catch (err) {
        throw err
    }
}

async function update(user) {
    user = await httpService.put(`user/${user._id}`, user)
    if (getLoggedinUser()._id === user._id) saveLocalUser(user)
    return user;
}

async function login(userCred) {
    try {
        const user = await httpService.post('auth/login', userCred)
        if (user) {
            socketService.emit('set-user-socket', user._id)
            return saveLocalUser(user)
        }
    } catch (err) {
        throw err
    }
}

async function signup(userCred) {
    try {
        const res = await httpService.post('auth/register', _createUser(userCred))
        return res
    } catch (err) {
        throw err
    }
}

async function isAdmin() {
    try {
        const res = await httpService.post('auth/isAdmin')
        if (res.status === 'ok') return 'ok'
    } catch (err) {
        console.log(err)
    }
}

async function createUser(userCred) {
    try {
        const content = { userCred }
        const res = await httpService.post('users/create', content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot create user')
    } catch (err) {
        throw err
    }
}

async function updateUser(updatedUser) {
    try {
        const content = { updatedUser }
        const res = await httpService.post('users/update', content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot update user')
    } catch (err) {
        throw err
    }
}

async function changeUserPassByEmail(email, password) {
    try {
        const content = { email, password }
        const res = await httpService.post('users/update_user_pass', content)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot change password')
    } catch (err) {
        throw err
    }
}

function getContactPurchaseType(user) {
    if (user) {
        return user.contactTransactions.filter(transaction => transaction.type === contactTransType.contactPurchase)
    } else return []
}

function getContactSaleType(user) {
    if (user) {
        return user.contactTransactions.filter(transaction => transaction.type === contactTransType.contactSale)
    } else return []
}

function getAgentUsers(users) {
    if (!users) return false
    return users.filter(user => isAgentUser(user))
}

function isAgentUser(user) {
    if (!user) return false
    else return user.permissions.includes(JsConst.AGENT)
}

function logout() {
    sessionStorage.removeItem(STORAGE_KEY_LOGGEDIN_USER)
    socketService.emit(constSocket.SOCKET_EMIT_LOGOUT);
    return httpService.post('auth/logout')
}

function clearLocalUser() {
    sessionStorage.removeItem(STORAGE_KEY_LOGGEDIN_USER)
}

function saveLocalUser(user) {
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, JSON.stringify(user))
    return user
}

function getLoggedinUser() {
    return JSON.parse(sessionStorage.getItem(STORAGE_KEY_LOGGEDIN_USER))
}

function _createUser(user) {
    return new User(user)
}