import { useSelector } from "react-redux";
import styled from "styled-components";
import Contacts from "../../../components/Contact/Contacts";
import { useEffect, useState } from "react";

const Container = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-between;
`;

const Filter = styled.div`
    display: flex;
    width: 350px;
    gap: 15px;
    align-items: center;
`;

const Title = styled.h3``


const ContactRecommendedPage = () => {
    const { currentUser } = useSelector((state) => state.user)
    // const { categories, jobTitles } = useSelector((state) => state.categoryManager)

    const [search, setSearch] = useState(null)

    useEffect(() => {
        loadUserSearchHistory()
    }, [])

    const loadUserSearchHistory = () => {
        if (currentUser.searchHistory.length) {
            setSearch(
                {
                    cat: currentUser.searchHistory[0].category,
                    filters: {
                        jobTitle: currentUser.searchHistory[0].jobTitle,
                        inStock: true
                    }
                })
        } else {
            setSearch(
                {
                    cat: 'banking',
                    filters: {
                        jobTitle: 'CISO',
                        inStock: true
                    }
                })
        }
    }

    // const handleFilters = ({ target }) => {
    // const { value, name } = target
    // setFilters({ ...filters, [name]: value, });
    // };

    return (
        <Container>
            <Filter>
                {/* <OptionsSelect
                    options={categories}
                    cb={handleFilters}
                    accessor={JsConst.TITLE}
                    name={'cat'} />

                <OptionsSelect
                    options={jobTitles}
                    cb={handleFilters}
                    accessor={JsConst.TITLE}
                    name={JsConst.JOBTITLE} />

                <button className="btn primary-btn">Search</button> */}
                <Title>By your previous search:</Title>
            </Filter>
            {search && <Contacts cat={search.cat} filters={search.filters} />}
        </Container>
    );
}

export default ContactRecommendedPage