import { DataGrid } from "@mui/x-data-grid";
import { constTranslate } from "../../../constant/translate";
import { DUMMY_USER_URL } from "../../../config/appConfig";
import { utilService } from "../../../services/util.service";
import { Tooltip } from "@mui/material";

export const UserAgentMessagesTable = ({ messages }) => {

    const columns = [
        {
            field: "category",
            headerName: "Category ",
            width: 150,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        <img className="userListImg" src={params.row?.contact.img?.url || DUMMY_USER_URL} alt="user" />
                        {params.row.contact.category}
                    </div>
                );
            },
        },
        {
            field: "company",
            headerName: "Company ",
            width: 150,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {params.row.contact.company}
                    </div>
                );
            },
        },
        {
            field: "jobTitle",
            headerName: "Job Title",
            width: 100,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {params.row.contact.jobTitle}
                    </div>
                );
            },
        },
        {
            field: "createdAt",
            headerName: "Created At",
            width: 100,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {utilService.getFormattedDate(new Date(params.row.createdAt))}
                    </div>
                );
            },
        },
        {
            field: "UserCategory",
            headerName: "User Category",
            width: 150,
            renderCell: (params) => {
                return (
                    <p className="text single-line">
                        {params.row.userCategory}
                    </p>
                );
            },
        },
        {
            field: "UserCompany",
            headerName: "User Company",
            width: 150,
            renderCell: (params) => {
                return (
                    <p className="text single-line">
                        {params.row.userCompany}
                    </p>
                );
            },
        },
        {
            field: "UserJobTitle",
            headerName: "User Job Title",
            width: 150,
            renderCell: (params) => {
                return (
                    <p className="text single-line">
                        {params.row.userJobTitle}
                    </p>
                );
            },
        },
        {
            field: "UserLinkedInUrl",
            headerName: "User LinkedIn",
            width: 150,
            renderCell: (params) => {
                return (
                    <Tooltip title={`${params.row.userLinkedInUrl}`}>
                        <p className="text single-line">
                            {params.row.userLinkedInUrl}
                        </p>
                    </Tooltip>
                );
            },
        },
        {
            field: "message",
            headerName: "Message",
            width: 150,
            renderCell: (params) => {
                return (
                    <Tooltip title={`${params.row.message}`}>
                        <p className="text single-line">
                            {params.row.message}
                        </p>
                    </Tooltip>
                );
            },
        },
    ]


    return (
        <div style={{ height: "400px" }}>
            <h3 className="mb-10">{constTranslate.AGENT_MESSAGES}</h3>
            <DataGrid
                rows={messages}
                disableSelectionOnClick
                columns={columns}
                getRowId={(row) => row._id}
                pageSize={10}
                style={{ height: "100%" }}
            />
        </div>
    )
}