import { constTranslate } from "../../../constant/translate";

export function getColumns(setModalProps) {
    return [
        {
            field: "userName",
            headerName: "Username",
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {params?.row?.username || constTranslate.UNKNOWN}
                    </div>
                );
            },
        },
        {
            field: "action",
            headerName: "Action",
            width: 150,
            renderCell: (params) => {
                return (
                    <button
                        className="contactListEdit"
                        onClick={() => setModalProps({ feedbacks: params.row.feedbacks })}>
                        {constTranslate.FEEDBACKS}
                    </button>
                );
            },
        },
    ]
} 