import "./newUser.css";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { constLang } from "../../../constant/language";
import { createUser } from "../../../redux/user/apiCalls";
import { OptionsSelect } from "../../../components/OptionsSelect";
import { JsConst } from "../../../constant/JsConst";
import { constTranslate } from "../../../constant/translate";
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService";

export default function NewUser() {
  const [form, setForm] = useState({
    [JsConst.IS_ACTIVE]: false,
    [JsConst.PERMISSIONS]: [],
    [JsConst.GENDER]: 'other',
  })

  const dispatch = useDispatch()
  const navigate = useNavigate()

  const handleChange = ({ target }) => {
    let { value, name } = target
    if (name === JsConst.PERMISSIONS) {
      value = handleUserTypeSelection(value)
    }
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const handleSelectChange = (name, value) => {
    if (name === JsConst.IS_ACTIVE) {
      setForm((prevState) => ({ ...prevState, [name]: value === constTranslate.YES ? true : false }))
    } else {
      setForm((prevState) => ({ ...prevState, [name]: value === constTranslate.AGENT ? [JsConst.AGENT] : [] }))
    }
  }

  const handleUserTypeSelection = (value) => {
    const permissions = []
    if (value === constTranslate.NO_PERMISSION) return permissions
    else {
      permissions.push(value.toLowerCase())
      return permissions
    }
  }

  const handleSubmit = async (ev) => {
    ev.preventDefault()
    try {
      await createUser(dispatch, form)
      showSuccessMsg('User created successfully')
      navigate('/admin/users')
    } catch (err) {
      showErrorMsg('Cannot create user')
    }
  }

  return (
    <div className="newUser">
      <h1 className="newUserTitle">New User</h1>
      <form className="newUserForm" onSubmit={handleSubmit}>
        <div className="newUserItem">
          <label>Username</label>
          <input type="text" placeholder="john" name={constLang.USERNAME} onChange={handleChange} required={true} />
        </div>
        <div className="newUserItem">
          <label>Full Name</label>
          <input type="text" placeholder="John Smith" name={constLang.FULLNAME} onChange={handleChange} required={true} />
        </div>
        <div className="newUserItem">
          <label>Email</label>
          <input type="email" placeholder="john@gmail.com" name={constLang.EMAIL} onChange={handleChange} required={true} />
        </div>
        <div className="newUserItem">
          <label>Password</label>
          <input type="password" placeholder="password" name={constLang.PASSWORD} onChange={handleChange} required={true} />
        </div>
        <div className="newUserItem">
          <label>Phone</label>
          <input type="text" placeholder="+1 123 456 78" name={constLang.PHONE} onChange={handleChange} />
        </div>
        <div className="newUserItem">
          <label>Address</label>
          <input type="text" placeholder="New York | USA" name={constLang.ADDRESS} onChange={handleChange} />
        </div>
        <div className="newUserItem">
          <label>User Type</label>
          <OptionsSelect
            options={[{ accessor: constTranslate.AGENT }, { accessor: constTranslate.NO_PERMISSION }]}
            cb={handleSelectChange}
            accessor={'accessor'}
            name={JsConst.PERMISSIONS} />
        </div>
        <div className="newUserItem">
          <label>Gender</label>
          <div className="newUserGender">
            <input type="radio" name={constLang.GENDER} id="male" value="male" onChange={handleChange} />
            <label htmlFor="male">Male</label>
            <input type="radio" name={constLang.GENDER} id="female" value="female" onChange={handleChange} />
            <label htmlFor="female">Female</label>
            <input type="radio" name={constLang.GENDER} id="other" value="other" onChange={handleChange} />
            <label htmlFor="other">Other</label>
          </div>
        </div>
        <div className="newUserItem">
          <label>Active</label>
          {/* <select className="newUserSelect" name={constLang.ACTIVE} id="active" onChange={handleChange} >
            <option value="true">Yes</option>
            <option value="false">No</option>
          </select> */}

          <OptionsSelect
            options={[{ accessor: constTranslate.YES }, { accessor: constTranslate.NO }]}
            cb={handleSelectChange}
            accessor={'accessor'}
            name={JsConst.IS_ACTIVE} />
        </div>
        <button className="newUserButton">Create</button>
      </form>
    </div>
  );
}
