import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import { getCategories } from "../redux/categoryManager/apiCalls";
import { mobile } from "../responsive";
import CategoryItem from "./CategoryItem";
import { useState } from "react";

import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft';

const Container = styled.div`
  display: flex;
  padding: 20px;
  justify-content: space-between;
  ${mobile({ padding: "0px", flexDirection: "column" })}
`;

const Arrow = styled.span`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  
  &>* {
    font-size: 45px !important;
    cursor: pointer;
    border-radius: 50%;
  }

  &>*:hover {
    background-color: #E8E8E8;
    transition: background-color 0.2s ease;
  }
`

const Categories = () => {
  const { categories } = useSelector(state => state.categoryManager)
  const [index, setIndex] = useState(0)

  const dispatch = useDispatch()
  const ARRAY_LENGTH = 3

  useEffect(() => {
    if (!categories.length) loadCategories()
  }, [])

  const loadCategories = async () => {
    try {
      await getCategories(dispatch)
    } catch (err) {
      console.log('Cannot get categories')
    }
  }

  const onNext = () => {
    if (index + ARRAY_LENGTH === categories.length) return
    setIndex(prevState => prevState + 1)
  }

  const onPrevious = () => {
    if (!index) return
    setIndex(prevState => prevState - 1)
  }

  return (
    <Container>
      <Arrow onClick={onPrevious}><KeyboardArrowLeftIcon /></Arrow>
      {categories.slice(index, index + ARRAY_LENGTH).map((item) => (
        <CategoryItem key={item._id} item={item} />
      ))}
      <Arrow onClick={onNext}><ChevronRightIcon /></Arrow>
    </Container>
  );
};

export default Categories;
