import "./ContactListAdmin.css";
import { DataGrid } from "@mui/x-data-grid";
import { DeleteOutline } from "@mui/icons-material";
import { Link } from "react-router-dom";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { deleteContact, getContacts } from "../../../redux/contact/apiCalls";
import { JsConst } from "../../../constant/JsConst";
import { DUMMY_USER_URL } from "../../../config/appConfig";
import { constContact } from "../../../model/contact/constContact";
import { useState } from "react";
import { contactTransactionService } from "../../../services/contactTransaction";
import { constTranslate } from "../../../constant/translate";
import { Modal } from "../../../components/Modal";
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService";
import { ContactTransactionListModal } from "../../../components/Modal/ContactTransactionListModal";
import { OptionsSelect } from "../../../components/OptionsSelect";
import { ConstRoutes } from "../../../constant/routes";

export default function ContactListAdmin() {
    const dispatch = useDispatch();
    const { contacts } = useSelector((state) => state.contacts);
    const { currentUser } = useSelector((state) => state.user);
    const { categories, companies } = useSelector(state => state.categoryManager)

    const [filteredContacts, setFilteredContacts] = useState(contacts || [])
    const [modalProps, setModalProps] = useState(null)
    const [filterBy, setFilterBy] = useState({
        selectedCategory: '',
        selectedCompany: ''
    })

    useEffect(() => {
        getContacts(dispatch)
    }, [])

    useEffect(() => {
        if (contacts.length) setFilteredContacts(contacts)
    }, [contacts])

    const handleDelete = async (id) => {
        const res = window.confirm('Are you sure you want to delete?')
        if (!res) return
        try {
            deleteContact(dispatch, id, currentUser)
            showSuccessMsg('Contact deleted!')
        } catch (err) {
            showErrorMsg('Cannot delete contact')
        }
    }

    const columns = [
        {
            field: "company",
            headerName: "Company",
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        <img className="contactListImg" src={params.row?.img?.url || DUMMY_USER_URL} alt="" />
                        {params.row.company}
                    </div>
                );
            },
        },
        { field: JsConst.CATEGORY, headerName: "Category", width: 220 },
        { field: JsConst.JOBTITLE, headerName: "Job Title", width: 220 },
        { field: JsConst.NAME, headerName: "Name", width: 220 },
        { field: JsConst.FAMILY_NAME, headerName: "Family Name", width: 220 },
        {
            field: JsConst.PRICE,
            headerName: "Price",
            width: 160,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {params.row.price}$
                    </div>
                );
            },
        },
        {
            field: JsConst.BUYER,
            headerName: constTranslate.BUYERS,
            width: 160,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {
                            params.row.transactionHistory?.length ?
                                <span className="link" onClick={() => setModalProps(params.row)}>
                                    {params.row?.transactionHistory.length} {constTranslate.BUYERS}
                                </span>
                                :
                                <span>{constTranslate.NO_BUYERS}</span>
                        }
                    </div>
                );
            },
        },
        {
            field: "action",
            headerName: "Action",
            width: 150,
            renderCell: (params) => {
                return (
                    <>
                        <Link to={"/admin/contact/" + params.row._id}>
                            <button className="contactListEdit">Edit</button>
                        </Link>
                        <DeleteOutline
                            className="contactListDelete"
                            onClick={() => handleDelete(params.row._id)}
                        />
                    </>
                );
            },
        },
    ]

    const getFilterContacts = (filterBy) => {
        switch (filterBy) {
            case 'notInStock':
                const notInStockContacts = contacts.filter(contact => !contact[constContact.IN_STOCK])
                return setFilteredContacts(notInStockContacts)
            case 'inStock':
                const inStockContacts = contacts.filter(contact => contact[constContact.IN_STOCK])
                return setFilteredContacts(inStockContacts)
            case 'purchased':
                const purchasedContacts = contacts.filter(contact => contact?.transactionHistory?.length)
                setFilteredContacts(purchasedContacts)
                break
            default:
                setFilteredContacts(contacts)
                setFilterBy({ selectedCategory: '', selectedCompany: '' })
        }
    }

    const getOrderContact = async () => {
        const res = await contactTransactionService.query()
        let transactions = res.filter(transaction => {
            return !transaction.contact.inStock
        })
        transactions = transactions.map(transaction => {
            return transaction.contact
        })
        const trans = contactTransactionService.removeDuplicateObjects(transactions)
        setFilteredContacts(trans)
    }

    const getCompanyByCategory = (companies) => {
        if (filterBy?.selectedCategory) {
            return companies.filter(company => company[JsConst.CATEGORY] === filterBy.selectedCategory.toLowerCase())
        } else return companies
    }

    const handleSelectChange = (name, value) => {
        if (name === JsConst.CATEGORY) {
            setFilterBy((prevState) => ({ ...prevState, selectedCategory: value }))
        } else {
            setFilterBy((prevState) => ({ ...prevState, selectedCompany: value }))
            const filteredContacts = contacts.filter(contact => {
                return contact[constContact.COMPANY] === value
            })
            setFilteredContacts(filteredContacts)
        }
    }

    return (
        <div className="contactList">
            <div className="btn-container">
                <button className="btn primary-btn" onClick={() => getFilterContacts('notInStock')}>Not in stock</button>
                <button className="btn primary-btn" onClick={() => getFilterContacts('inStock')}>In stock</button>
                <button className="btn primary-btn" onClick={() => getOrderContact()}>Order not in stock</button>
                <button className="btn primary-btn" onClick={() => getFilterContacts('purchased')}>Purchased Contacts</button>
                <button className="btn primary-btn" onClick={() => getFilterContacts(null)}>Clear Filters</button>
            </div>

            <div className="flex justify-between align-center">
                <div className="btn">
                    <Link to={ConstRoutes.ADMIN_NEW_CONTACT}>
                        <button className="contactAddButton">Create New Contact</button>
                    </Link>
                </div>

                <div className="flex m-gap m-15" style={{ width: '50%' }}>
                    <div className="addContactItem">
                        <div className="label-container">
                            <label>{constTranslate.CATEGORIES}</label>
                        </div>
                        <OptionsSelect
                            options={categories}
                            cb={handleSelectChange}
                            accessor={JsConst.TITLE}
                            name={JsConst.CATEGORY}
                            selectedValue={filterBy.selectedCategory}
                        />
                    </div>

                    <div className="addContactItem">
                        <div className="label-container">
                            <label>{constTranslate.COMPANY}</label>
                        </div>
                        <OptionsSelect
                            options={getCompanyByCategory(companies)}
                            cb={handleSelectChange}
                            accessor={JsConst.COMPANY}
                            name={JsConst.COMPANY}
                            selectedValue={filterBy.selectedCompany}
                        />
                    </div>
                </div>
            </div>

            <DataGrid
                rows={filteredContacts}
                disableSelectionOnClick
                columns={columns}
                getRowId={(row) => row._id}
                pageSize={20}
            />

            {
                modalProps && <Modal setModalProps={setModalProps} >
                    <ContactTransactionListModal modalProps={modalProps} />
                </Modal>
            }
        </div>
    )
}