import { paymentService } from "../../services/payment.service";
import { deleteContactTransaction, saveCurrentUser } from "../user/userRedux";
import { addCredit } from "./cartRedux";


export const addCreditPackage = async (dispatch, credit) => {
    try {
        dispatch(addCredit(credit))
    } catch (err) {
        throw err
    }
};

export const createTransaction = async (dispatch, creditCart) => {
    try {
        const updatedUser = await paymentService.create(creditCart)
        if (updatedUser) dispatch(saveCurrentUser(updatedUser))
    } catch (err) {
        throw err
    }
}

export const createContactTransaction = async (dispatch, contact) => {
    try {
        const updatedUser = await paymentService.createContactPurchase(contact)
        if (updatedUser) dispatch(saveCurrentUser(updatedUser))
    } catch (err) {
        throw err
    }
}

export const removeContactTransaction = async (dispatch, transaction, userId) => {
    try {
        const res = await paymentService.removeContact(transaction._id, userId)
        if (res === 'ok') dispatch(deleteContactTransaction(transaction))
    } catch (err) {
        throw err
    }
}