import styled from "styled-components";
import { useState } from "react";
import { login } from "../../redux/user/apiCalls";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { showErrorMsg } from "../../services/eventBusService";
import { useEffectUpdate } from "../../hook/useEffectUpdate"
import { OneTimePassword } from "../../components/Login/OneTimePassword";
import { ChangePassword } from "../../components/Login/ChangePassword";
import { LoginComponent } from "../../components/Login/LoginComponent";
import { ConstRoutes } from "../../constant/routes";

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url("https://images.pexels.com/photos/6984650/pexels-photo-6984650.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940")
      center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Login = () => {
  const { error } = useSelector((state) => state.app);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false)
  const [type, setType] = useState('login')
  const [email, setEmail] = useState('')

  const dispatch = useDispatch();
  const navigate = useNavigate()

  useEffectUpdate(() => {
    if (error.type) showErrorMsg(`${error.type} ${error.msg}`)
  }, [error.type])

  const onSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true)
    try {
      const user = await login(dispatch, { username, password })
      setIsLoading(false)
      if (user) navigate(ConstRoutes.HOME)
    } catch (err) {
      const errorMessage = err?.response?.data?.errorMessage
      setIsLoading(false)
      showErrorMsg(errorMessage)
    }
  };

  return (
    <Container>
      <DynamicComponent
        type={type}
        setType={setType}
        onSubmit={onSubmit}
        setUsername={setUsername}
        setPassword={setPassword}
        isLoading={isLoading}
        error={error}
        setEmail={setEmail}
        email={email}
      />
    </Container>
  );
};

const DynamicComponent = (props) => {
  switch (props.type) {
    case 'login':
      return <LoginComponent {...props} />
    case 'otp':
      return <OneTimePassword {...props} />
    case 'password':
      return <ChangePassword {...props} />
    default:
      break;
  }
}

export default Login;
