import { DataGrid } from "@mui/x-data-grid"
import { useEffect, useState } from "react"
import { showErrorMsg } from "../../../services/eventBusService";
import { UserMsg } from "../../../constant/userMessage";
import { feedbackService } from "../../../services/feedback.service";
import { getColumns } from "./FeedbacksColumn";
import { Modal } from "../../../components/Modal";
import { ContactsFeedback } from "./ContactsFeedback";

const Feedbacks = () => {
    const [users, setUsers] = useState([])
    const [modalProps, setModalProps] = useState(null)

    useEffect(() => {
        loadFeedbacks()
    }, [])

    const loadFeedbacks = async () => {
        try {
            const users = await feedbackService.getUserFeedback()
            setUsers(users)
        } catch (error) {
            showErrorMsg(UserMsg.CANNOT_GET_FEEDBACKS)
        }
    }

    return (
        <div className="contactList">
            <DataGrid
                rows={users}
                disableSelectionOnClick
                columns={getColumns(setModalProps)}
                getRowId={(row) => row.username}
                pageSize={10}
            />
            {
                modalProps && <Modal setModalProps={setModalProps} style={{ width: '80%', maxWidth: '1200px', height: '400px' }} isClosingOutside={false}>
                    <ContactsFeedback feedbacks={modalProps.feedbacks} />
                </Modal>
            }
        </div>
    )
}


export default Feedbacks