import "./newContact.css";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { JsConst } from "../../../constant/JsConst";
import { OptionsSelect } from "../../../components/OptionsSelect";
import { Modal } from "../../../components/Modal";
import { FileUploader } from "../../../components/FileUploader";
import { AddNewCategoryModal } from "../../../components/Modal/AddNewCategoryModal";
import { deleteCategoryAction, deleteCompanyAction, deleteJobTitleAction, deleteTerritoryAction } from "../../../redux/categoryManager/apiCalls";
import { createContact } from "../../../redux/contact/apiCalls";
import { territoryConst } from "../../../constant/Model/territoryConst";
import { getUsers } from "../../../redux/user/apiCalls";
import { constUser } from '../../../model/user/constUser'
import { constContact } from "../../../model/contact/constContact";
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService";
import { constTranslate } from "../../../constant/translate";
import { utilService } from "../../../services/util.service";
import { userService } from "../../../services/user.service";
import { Contact } from "../../../model/contact/Contact";
import { Email } from "../../../model/email/Email";


export default function NewContact() {
    const { currentUser, users } = useSelector(state => state.user)
    const { categories, jobTitles, companies, territories } = useSelector(state => state.categoryManager)

    const [modalProps, setModalProps] = useState(null)
    const [selectedCat, setSelectedCat] = useState(null)
    const [inputs, setInputs] = useState({})

    const dispatch = useDispatch()
    const navigate = useNavigate()

    useEffect(() => {
        if (!users.length) loadUsers()
    }, [])

    const loadUsers = () => {
        try {
            getUsers(dispatch)
        } catch (err) {
            showErrorMsg('Cannot get users')
        }
    }

    const handleChange = ({ target }) => {
        let { name, value } = target
        if (name === JsConst.EMAIL_URL) {
            return setInputs((prevState) => ({ ...prevState, email: { ...prevState.email, [name]: value } }))
        }
        setInputs((prev) => ({ ...prev, [name]: value }));
    };

    const handleSelectChange = (name, value) => {
        if (name === JsConst.CATEGORY) {
            setSelectedCat(value)
        }
        else if (name === JsConst.EMAIL_TYPE) {
            return setInputs((prevState) => ({ ...prevState, email: { ...prevState.email, [name]: value } }))
        }
        else if (name === constContact.AGENT) {
            const agent = utilService.getAgent(users, value)
            return setInputs((prevState) => ({ ...prevState, [name]: agent }))
        }
        else if (name === JsConst.IN_STOCK) {
            return setInputs((prevState) => ({ ...prevState, [name]: value === constTranslate.NO ? false : true }))
        }
        return setInputs((prevState) => ({ ...prevState, [name]: value }))
    }

    const handleSubmit = async (ev) => {
        ev.preventDefault()
        const res = window.confirm('Are you sure?')
        if (!res) return
        try {
            const form = { ...inputs }
            if (form.email) form.email = new Email(form.email)
            const newContact = new Contact(form)
            newContact.emails.push(form.email)

            await createContact(dispatch, newContact, currentUser)
            showSuccessMsg('Contact added successfully')
            navigate('/admin/contacts')
        } catch (err) {
            showErrorMsg('Cannot create contact')
        }
    }


    const uploadContactImage = (value, name) => {
        setInputs((prev) => ({ ...prev, [name]: value }));
    }

    const getCompanyByCategory = (companies) => {
        if (companies && selectedCat) {
            return companies.filter(company => company[JsConst.CATEGORY] === selectedCat.toLowerCase())
        } else return companies
    }

    const onDeleteOption = (name, option) => {
        const res = window.confirm(`Are you sure you want to delete?`)
        if (!res) return
        switch (name) {
            case JsConst.CATEGORY:
                return deleteCategoryAction(dispatch, option._id)
            case JsConst.COMPANY:
                return deleteCompanyAction(dispatch, option._id)
            case JsConst.JOBTITLE:
                return deleteJobTitleAction(dispatch, option._id)
            case JsConst.territory:
                return deleteTerritoryAction(dispatch, option._id)
            default:
                break;
        }
    }

    const getModalProps = () => {
        return {
            setModalProps,
            categories
        }
    }


    return (
        <div className="newContact">
            <h1 className="addContactTitle">{constTranslate.NEW_CONTACT}</h1>
            <form className="addContactForm" onSubmit={handleSubmit}>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.CATEGORIES}</label>
                        <span onClick={() => setModalProps({ type: JsConst.CATEGORIES, idx: 2 })}>{constTranslate.ADD_NEW}</span>
                    </div>
                    <OptionsSelect
                        options={categories}
                        cb={handleSelectChange}
                        removeCb={onDeleteOption}
                        accessor={JsConst.TITLE}
                        name={JsConst.CATEGORY}
                        isRequired={true} />
                </div>

                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.COMPANY}</label>
                        <span onClick={() => setModalProps({ type: JsConst.COMPANIES, idx: 3 })}>{constTranslate.ADD_NEW}</span>
                    </div>
                    <OptionsSelect
                        options={getCompanyByCategory(companies)}
                        cb={handleSelectChange}
                        removeCb={onDeleteOption}
                        accessor={JsConst.COMPANY}
                        name={JsConst.COMPANY}
                        isRequired={true} />
                </div>

                <div className="addContactItem">
                    <label>{constTranslate.NAME}</label>
                    <input name={JsConst.NAME} type="text" placeholder="John" onChange={handleChange} required={true} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.FAMILY_NAME}</label>
                    <input name={JsConst.FAMILY_NAME} type="text" placeholder="John" onChange={handleChange} required={true} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.JOB_TITLES}</label>
                        <span onClick={() => setModalProps({ type: JsConst.JOBTITLE, idx: 0 })}>{constTranslate.ADD_NEW}</span>
                    </div>
                    <OptionsSelect
                        options={jobTitles}
                        cb={handleSelectChange}
                        removeCb={onDeleteOption}
                        accessor={JsConst.TITLE}
                        name={JsConst.JOBTITLE}
                        isRequired={true} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>{constTranslate.COUNTRY}</label>
                        <span onClick={() => setModalProps({ type: JsConst.COUNTRY, idx: 1 })}>{constTranslate.ADD_NEW}</span>
                    </div>
                    <OptionsSelect
                        options={territories}
                        cb={handleSelectChange}
                        removeCb={onDeleteOption}
                        accessor={territoryConst.TERRITORY_NAME}
                        name={JsConst.COUNTRY}
                        isRequired={true} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.DESCRIPTION}</label>
                    <input name={JsConst.DESC} type="text" placeholder="description..." onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.EMAIL_TYPE}</label>
                    <OptionsSelect
                        options={[{ _id: constTranslate.PERSONAL }, { _id: constTranslate.WORK }]}
                        cb={handleSelectChange}
                        accessor={JsConst._ID}
                        name={JsConst.EMAIL_TYPE}
                        isRequired={true} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.EMAIL}</label>
                    <input name={JsConst.EMAIL_URL} type="text" placeholder="john@gmail.com" onChange={handleChange} required={true} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.PHONE}</label>
                    <input name={JsConst.PHONE} type="text" placeholder="+972.." onChange={handleChange} required={true} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.PRICE}</label>
                    <input name={JsConst.PRICE} type="number" placeholder="100" onChange={handleChange} required={true} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.LINKEDIN_LINKS}</label>
                    <input name={JsConst.LINKEDIN_LINK} type="text" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.IN_STOCK}</label>
                    <OptionsSelect
                        options={[{ _id: constTranslate.YES }, { _id: constTranslate.NO }]}
                        cb={handleSelectChange}
                        accessor={JsConst._ID}
                        name={JsConst.IN_STOCK}
                        selectedValue={inputs.inStock ? constTranslate.YES : constTranslate.NO}
                        isRequired={true} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.AGENT}</label>
                    <OptionsSelect
                        options={userService.getAgentUsers(users)}
                        cb={handleSelectChange}
                        accessor={constUser.FULLNAME}
                        name={constContact.AGENT} />
                </div>
                <div className="addContactItem">
                    <label>{constTranslate.IMAGE}</label>
                    <FileUploader cb={uploadContactImage} name={JsConst.IMG} />
                </div>
                <div className="add-contact-button--container">
                    <button className="addContactButton">
                        {constTranslate.CREATE}
                    </button>
                </div>
            </form>
            {
                modalProps && <Modal {...getModalProps()} >
                    <AddNewCategoryModal {...modalProps} {...getModalProps()} />
                </Modal>
            }
        </div>
    );
}