import { useCallback, useEffect } from "react"
import { showErrorMsg } from "../../../services/eventBusService"
import { UserMsg } from "../../../constant/userMessage"
import { supportChatService } from "../../../services/supportChat.service"
import { useState } from "react"
import { DUMMY_USER_URL } from "../../../config/appConfig"
import { Chat } from "./Chat"


const UserSupportChat = () => {
    const [chats, setChats] = useState([])
    const [selectedChat, setSelectedChat] = useState(null)

    useEffect(() => {
        loadChats()
    }, [])

    const loadChats = useCallback(async () => {
        try {
            const supportChats = await supportChatService.query()
            if (supportChats.length) setChats(supportChats)
        } catch (error) {
            showErrorMsg(UserMsg.CANNOT_GET_CHATS)
        }
    }, [])


    const handleSelectedChat = useCallback((chat) => {
        setSelectedChat(chat)
    }, [])

    return (
        <section className="userSupportChat-container">
            <ul className="chatList-container">
                {
                    chats.map(chat => {
                        return (
                            <li key={chat._id} onClick={() => handleSelectedChat(chat)} className={selectedChat && (chat._id === selectedChat._id) ? "selected" : ""}>
                                <div className="chatList-img">
                                    <img src={chat.chatImg || DUMMY_USER_URL} alt="" />
                                </div>
                                <p>{chat.fullname}</p>
                            </li>
                        )
                    })
                }
            </ul>

            <div className="chatRoom-container">
                <Chat chat={selectedChat} />
            </div>

        </section>
    )
}


export default UserSupportChat