import { makeStyles } from "@material-ui/core";
import { Delete } from "@mui/icons-material";
import { IconButton, MenuItem, Select } from "@mui/material";
import { useEffect } from "react";
import { useState } from "react";

const useStyles = makeStyles(() => ({
    menu: {
        maxHeight: 200,
        overflowY: 'auto',
    },
    menuItem: {
        display: 'flex',
        justifyContent: 'space-between',
    },
    deleteButton: {
        marginLeft: 8,
    },
}));

export const OptionsSelect = (props) => {
    const { options, cb, accessor, name, removeCb, value, selectedValue, isDisabled, style, isRequired } = props
    const [selectedOption, setSelectedOption] = useState(selectedValue || '');
    const [cat, setCat] = useState(options || [])
    const classes = useStyles();

    useEffect(() => {
        if (selectedValue || selectedValue === '') setSelectedOption(selectedValue)
    }, [selectedValue])

    useEffect(() => {
        if (options) setCat(options)
    }, [options])

    const handleSelectChange = (ev) => {
        const { name, value } = ev.target
        setSelectedOption(value);
        if (cb) cb(name, value)
    };

    const onDeleteOption = async (ev, option) => {
        ev.stopPropagation()
        removeCb(name, option)
    }

    return (
        <div className="select-container">
            <Select
                MenuProps={{ classes: { paper: classes.menu } }}
                disabled={isDisabled}
                onChange={handleSelectChange}
                value={selectedOption}
                name={name}
                style={style}
                required={isRequired}
            >
                {cat.map((option, idx) => {
                    return <MenuItem key={idx} value={option[value] || option[accessor]} className={classes.menuItem} >
                        {option[accessor]}
                        {selectedOption !== option[accessor] && removeCb &&
                            <IconButton className={classes.deleteButton} onClick={(ev) => onDeleteOption(ev, option)}>
                                <Delete />
                            </IconButton>}
                    </MenuItem>
                })}
            </Select>
        </div>
    );
}