import styled from "styled-components";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { DUMMY_USER_URL } from "../../config/appConfig";
import { mobile } from "../../responsive";
import { constLang } from "../../constant/language";
import { constTranslate } from "../../constant/translate";
import { Remove } from "@material-ui/icons";
import { removeContactTransaction } from "../../redux/cart/apiCalls";
import { showErrorMsg, showSuccessMsg } from "../../services/eventBusService";
import { fetchData, fetchDataSuccess } from "../../redux/app/appRedux";
import { useParams } from "react-router-dom";
import { userService } from "../../services/user.service";
import { ContactInfoModal } from "../Contact/ContactInfoModal";
import { utilService } from "../../services/util.service";


const Info = styled.div`
  flex: 3;
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

const Product = styled.div`
  display: flex;
  ${mobile({ flexDirection: "column" })}
`;

const ProductDetail = styled.div`
  flex: 2;
  display: flex;
  cursor: pointer;
`;

const Image = styled.img`
  width: 25%;
  min-width: 136px;
  max-width:250px;
  object-fit: fill;
  aspect-ratio: 3/2;
`;

const Details = styled.div`
display: flex;
flex-direction: column;
justify-content: center;
`;

const ProductName = styled.span`
  padding: 5px 10px;
`;

const PriceDetail = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const ProductAmountContainer = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  cursor:pointer;
`;

const Hr = styled.hr`
  background-color: #eee;
  border: none;
  height: 1px;
`;


const Summary = styled.div`
  flex: 1;
  border: 0.5px solid lightgray;
  border-radius: 10px;
  padding: 20px;
  height: 50vh;
`;

const SummaryTitle = styled.h1`
  font-weight: 200;
`;

const SummaryItem = styled.div`
  margin: 30px 0px;
  display: flex;
  justify-content: space-between;
  font-weight: ${(props) => props.type === "total" && "500"};
  font-size: ${(props) => props.type === "total" && "24px"};
`;

const SummaryItemText = styled.span``;

const SummaryItemPrice = styled.span``;

export const ContactCart = () => {
  const { currentUser } = useSelector((state) => state.user)
  const [user, setUser] = useState(currentUser)
  const [modalProps, setModalProps] = useState(null)

  const dispatch = useDispatch()
  const { id } = useParams()

  useEffect(() => {
    if (id) loadUser()
    else setUser(currentUser)
  }, [currentUser])

  const loadUser = async () => {
    try {
      const res = await userService.getById(id)
      if (res.status === 'ok') setUser(res.content)
    } catch (err) {
      console.log(err)
    }
  }

  const onRemoveItem = async (transaction) => {
    if (!isUserOwnContact) return
    dispatch(fetchData())
    try {
      await removeContactTransaction(dispatch, transaction, user._id)
      if (id) await loadUser()
      dispatch(fetchDataSuccess())
      showSuccessMsg('Contact removed')
    } catch (err) {
      showErrorMsg('Cannot remove contact')
    }
  }

  const isUserOwnContact = (transaction) => {
    const userTrans = currentUser.contactTransactions.find(trans => trans._id === transaction._id)
    if (userTrans) return true
    else {
      showErrorMsg('You do not own this contact')
      return false
    }
  }

  const handleContactClick = (transaction) => {
    setModalProps(transaction)
  }

  const getContactTransactionLength = () => {
    if (user) return userService.getContactPurchaseType(user).length
    else return 0
  }

  return (
    <>
      <Info>
        {user && userService.getContactPurchaseType(user).map((transaction, idx) => {
          return <React.Fragment key={idx}>
            <Product>
              <ProductDetail onClick={() => handleContactClick(transaction)}>
                <Image src={transaction?.contact?.img?.url || DUMMY_USER_URL} />
                <Details>
                  <ProductName>
                    <b>{constTranslate.CATEGORY}:</b> {transaction.contact.category}
                  </ProductName>
                  <ProductName>
                    <b>{constLang.COMPANY}:</b> {transaction.contact.company}
                  </ProductName>
                  <ProductName>
                    <b>{constLang.Job_Title}:</b> {transaction.contact.jobTitle}
                  </ProductName>
                  <ProductName>
                    <b>{constLang.DATE}:</b> {utilService.getFormattedDate(new Date(transaction.createdAt))}
                  </ProductName>
                  <ProductName>
                    <b>{constLang.PRICE}:</b> {transaction.contact.price}$
                  </ProductName>
                </Details>
              </ProductDetail>
              {!transaction.contact.inStock && <PriceDetail>
                <ProductAmountContainer>
                  <Remove onClick={() => onRemoveItem(transaction)} />
                </ProductAmountContainer>
              </PriceDetail>}
            </Product>
            <Hr />
          </React.Fragment>
        })}
      </Info>
      <Summary>
        <SummaryTitle>CREDIT SUMMARY</SummaryTitle>
        <SummaryItem>
          <SummaryItemText>Total Credits</SummaryItemText>
          <SummaryItemPrice>{user ? user.credits : 0} Credits</SummaryItemPrice>
        </SummaryItem>
        <SummaryItem>
          <SummaryItemText>Contacts Amount</SummaryItemText>
          <SummaryItemPrice>{getContactTransactionLength()}</SummaryItemPrice>
        </SummaryItem>
        <SummaryItem>
          <SummaryItemText>Pending Contacts</SummaryItemText>
          <SummaryItemPrice>{user ? user.contactTransactions.filter(transaction => {
            return !transaction.contact.inStock
          }).length : 0}</SummaryItemPrice>
        </SummaryItem>
      </Summary>

      {modalProps && <ContactInfoModal setModalProps={setModalProps} modalProps={modalProps} />}
    </>
  )
}

