import { Badge } from "@material-ui/core";
import { Search, ShoppingCartOutlined } from "@material-ui/icons";
import React, { useEffect } from "react";
import styled from "styled-components";
import { mobile } from "../responsive";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { MenuItem } from "../constant/style";
import { userService } from "../services/user.service";

import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import ShareIcon from '@mui/icons-material/Share';
import AccessTimeFilledIcon from '@mui/icons-material/AccessTimeFilled';
import NotificationsIcon from '@mui/icons-material/Notifications';
import { socketService } from "../services/socket.service";
import { constSocket } from "../constant/socket";
import { useState } from "react";
import { Modal } from "./Modal";
import { UserAuthOptions } from "./UserAuthOptions";
import { Notifications } from "./Notification/Notifications";
import { constNotification } from "../model/notification/constNotification";
import { addNotification } from "../redux/user/apiCalls";
import { constUser } from "../model/user/constUser";


const Container = styled.div`
  height: 60px;
  ${mobile({ height: "50px" })}
`;

const Wrapper = styled.div`
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  ${mobile({ padding: "10px 0px" })}
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
`;

const Language = styled.span`
  font-size: 14px;
  cursor: pointer;
  ${mobile({ display: "none" })}
`;

const SearchContainer = styled.div`
  border: 0.5px solid lightgray;
  display: flex;
  align-items: center;
  margin-left: 25px;
  padding: 5px;
`;

const Input = styled.input`
  border: none;
  ${mobile({ width: "50px" })}
`;

const Center = styled.div`
  flex: 1;
  text-align: center;
`;

const Logo = styled.h1`
  font-weight: bold;
  ${mobile({ fontSize: "24px" })}
`;
const Right = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  ${mobile({ flex: 2, justifyContent: "center" })}
`;

const Navbar = () => {
  const { credit } = useSelector(state => state.cart)
  const { currentUser } = useSelector(state => state.user)
  const [modalProps, setModalProps] = useState(null)
  const dispatch = useDispatch()

  useEffect(() => {
    socketService.on(constSocket.SOCKET_ON_PURCHASE_CONTACT, handleNotifications)
  }, [])

  const handleNotifications = (notification) => {
    if (currentUser) {
      const updatedUser = updateNotifications(notification)
      addNotification(dispatch, updatedUser)
    }
  }

  const updateNotifications = (notification) => {
    let updatedUser = { ...currentUser }
    let updatedNotifications = [...currentUser[constUser.NOTIFICATIONS]]
    updatedNotifications.unshift(notification)
    updatedUser[constUser.NOTIFICATIONS] = updatedNotifications
    return updatedUser
  }

  const getUnreadUserNotifications = (user) => {
    const unreadNote = user?.notifications.filter(note => !note[constNotification.IS_READ])
    return unreadNote
  }

  return (
    <>
      <Container>
        <Wrapper>
          <Left>
            <Language>EN</Language>
            <SearchContainer>
              <Input placeholder="Search" />
              <Search style={{ color: "gray", fontSize: 16 }} />
            </SearchContainer>
          </Left>
          <Center>
            <Logo>Qleads</Logo>
          </Center>
          <Right>
            <UserAuthOptions user={currentUser} />
            <Link to="/cart/contact">
              <MenuItem>
                <Badge badgeContent={userService.getContactPurchaseType(currentUser).length} color="primary">
                  <ShoppingCartOutlined />
                </Badge>
              </MenuItem>
            </Link>
            <Link to="/contact/favorite">
              <MenuItem>
                <FavoriteBorderIcon />
              </MenuItem>
            </Link>
            <Link to="/credits">
              <MenuItem>
                <Badge badgeContent={credit.quantity} color="primary">
                  <MonetizationOnIcon />
                </Badge>
              </MenuItem>
            </Link>
            {
              userService.isAgentUser(currentUser) &&
              <Link to="/contact/requirements">
                <MenuItem>
                  <ShareIcon />
                </MenuItem>
              </Link>
            }
            {currentUser &&
              <Link to="/contact/recommended">
                <MenuItem>
                  <AccessTimeFilledIcon />
                </MenuItem>
              </Link>
            }
            {currentUser && <MenuItem onClick={() => setModalProps({ currentUser })}>
              <Badge badgeContent={getUnreadUserNotifications(currentUser)?.length || 0} color="primary">
                <NotificationsIcon />
              </Badge>
            </MenuItem>}
          </Right>
        </Wrapper>
      </Container >

      {
        modalProps &&
        <Modal setModalProps={setModalProps}>
          <Notifications modalProps={modalProps} />
        </Modal>
      }
    </>
  );
};



export default Navbar;
