import { ContactTransactionEP } from "../config/appConfig"
import { httpService } from "./http.service"


export const contactTransactionService = {
    query,
    removeDuplicateObjects,
    getUsersTransactionsByContactId
}

async function query(filterBy = {}) {
    try {
        const res = await httpService.get(ContactTransactionEP.default, filterBy)
        if (res.status === 'ok') return res.content
        else return { error: 'Cannot get contact transactions' }
    } catch (err) {
        throw err
    }
}

async function getUsersTransactionsByContactId(contactId) {
    try {
        const res = await httpService.post(ContactTransactionEP.getUsers, contactId)
        if (res.status === 'ok') return res.content
        else return { error: 'Cannot get users' }
    } catch (err) {
        throw err
    }
}

function removeDuplicateObjects(transactions) {
    const uniqueObjects = {};
    const result = [];

    for (let i = 0; i < transactions.length; i++) {
        const contact = transactions[i];
        const id = contact._id;

        if (!uniqueObjects[id]) {
            uniqueObjects[id] = true;
            result.push(contact);
        }
    }

    return result;
}