import styled from 'styled-components';
import { constTranslate } from '../../constant/translate';
import { useEffect, useRef, useState } from 'react';
import { authService } from '../../services/auth.service';
import { utilService } from '../../services/util.service';
import { JsConst } from '../../constant/JsConst';
import { LoadingButton } from '../Core/LoadingButton';
import { showErrorMsg } from '../../services/eventBusService';

const Container = styled.div`
  width: 28em;
  background-color: #ffffff;
  padding: 4em 2em;
  border-radius: 0.8em;
  box-shadow: 0 45px 60px rgba(30, 22, 1, 0.3);
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
  `;

const InputField = styled.div`
  width: 100%;
  display: flex;
  gap: 10px;
`

const Input = styled.input`
  height: 3em;
  width: 3em;
  border: 2px solid #dad9df;
  outline: none;
  text-align: center;
  font-size: 1.5em;
  border-radius: 0.3em;
  background-color: #ffffff;
  outline: none;

  &[type="number"]::-webkit-outer-spin-button,
  &[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

    &:disabled {
        color: #89888b;
    }

    &:focus {
        border: 1px solid teal;
    }
}
`

const Title = styled.h2`
  font-weight: 500;
`

const SubTitle = styled.span`
  color: teal;
`

const Button = styled.button`
  width: 40%;
  padding: 15px 20px;
  background-color: teal;
  color: white;
  cursor: pointer;
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  gap: 30px;
`

const TextContainer = styled.div`
`

const Text = styled.p`
  
`

const Link = styled.a`
  
`

const EmailInput = styled.input`
  flex: 1;
  min-width: 40%;
  margin: 10px 0;
  padding: 10px;
`;

export const OneTimePassword = ({ setType, setEmail, email }) => {
  const [timer, setTimer] = useState(60)
  const [disable, setDisable] = useState(false)
  const [OTPStatus, setOTPStatus] = useState([0, 0, 0, 0])
  const [proccess, setProccess] = useState(1)
  const [isLoading, setIsLoading] = useState(false)

  const EMAIL_PROCCESS = 1
  const OTP_PROCCESS = 2

  const inputRef1 = useRef(null)
  const inputRef2 = useRef(null)
  const inputRef3 = useRef(null)
  const inputRef4 = useRef(null)

  useEffect(() => {
    let interval = setInterval(() => {
      setTimer((prevState) => {
        prevState <= 1 && clearInterval(interval)
        if (prevState <= 1) setDisable(false)
        if (prevState <= 0) return prevState
        return prevState - 1
      })
    }, 1000)

    return () => clearInterval(interval)
  }, [disable])

  const handleChange = ({ target }, inputId) => {
    const { value, name } = target

    if (name === JsConst.EMAIL) {
      return setEmail(value)
    }

    setOTPStatus((prevState) => {
      prevState[name] = value
      return [...prevState]
    })

    handleFocus(inputId)
  }

  const handleFocus = (inputId) => {
    if (inputId === 1) inputRef2.current.focus()
    else if (inputId === 2) inputRef3.current.focus()
    else inputRef4.current.focus()
  }

  const verifyOTP = async (ev) => {
    ev.preventDefault()
    setIsLoading(true)
    try {
      const isValidOtp = await authService.isValidOTP(OTPStatus)
      if (isValidOtp) setType('password')
      else showErrorMsg('The OTP is wrong')
    } catch (err) {
      showErrorMsg('Failed to verify OTP')
    } finally {
      setIsLoading(false)
    }
  }

  const onSubmitOTP = async (ev) => {
    ev.preventDefault()
    setIsLoading(true)
    if (!utilService.isValidEmail(email)) {
      setProccess(EMAIL_PROCCESS)
      setIsLoading(false)
      setEmail('')
      return showErrorMsg('Invalid email')
    }

    try {
      await authService.sendRecoveryEmail(email, OTPStatus)
      setProccess(OTP_PROCCESS)
    } catch (err) {
      console.log(err)
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <Container>
      {
        OTP_PROCCESS === proccess ?
          <>
            <Title>{constTranslate.EMAIL_VERIFICATION}</Title>
            <SubTitle>We have sent a code to your email</SubTitle>

            <Form onSubmit={verifyOTP}>
              <InputField>
                <Input type="number" name="0" onChange={(ev) => handleChange(ev, 1)} ref={inputRef1} />
                <Input type="number" name="1" onChange={(ev) => handleChange(ev, 2)} ref={inputRef2} />
                <Input type="number" name="2" onChange={(ev) => handleChange(ev, 3)} ref={inputRef3} />
                <Input type="number" name="3" onChange={(ev) => handleChange(ev, 4)} ref={inputRef4} />
              </InputField>
              <LoadingButton isLoading={isLoading}>
                {constTranslate.SUBMIT}
              </LoadingButton>
            </Form>

            <TextContainer>
              <Text>Didn't recieve code?</Text>
              <Link>{disable ? `Resend OTP in ${timer}s` : "Resend OTP"}</Link>
            </TextContainer>
          </>
          :
          <>
            <Title>{constTranslate.PLEASE_FILL_YOUR_EMAIL}</Title>
            <Form onSubmit={onSubmitOTP}>
              <EmailInput type="text" name="email" onChange={handleChange} placeholder="Please enter you email" value={email} />
              <LoadingButton isLoading={isLoading}>
                {constTranslate.SEND_OTP}
              </LoadingButton>
            </Form>
          </>
      }
    </Container>
  )
}