import { DUMMY_USER_URL } from "../../../config/appConfig";
import { constTranslate } from "../../../constant/translate";

export function getColumns(setModalProps) {
    return [
        {
            field: "userName",
            headerName: "Username",
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        <img className="contactListImg" src={params.row?.imgUrl?.url || DUMMY_USER_URL} alt="" />
                        {params?.row?.username || constTranslate.UNKNOWN}
                    </div>
                );
            },
        },
        {
            field: "action",
            headerName: "Action",
            width: 250,
            renderCell: (params) => {
                return (
                    <button
                        className="contactListEdit"
                        onClick={() => setModalProps({ messages: params.row.agentMessages })}>
                        {constTranslate.AGENT_MESSAGES}
                    </button>
                );
            },
        },
    ]
} 