import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { logout, requiredAdmin } from "../redux/user/apiCalls";

import { ContainerFlex, MenuItem } from "../constant/style";

export const UserAuthOptions = ({ user }) => {
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const onLogout = () => {
        logout(dispatch);
    }

    const checkAuth = () => {
        if (user.isAdmin) {
            requiredAdmin(navigate)
        }
    }

    return (
        <ContainerFlex>
            <MenuItem>
                <Link to="/">HOME</Link>
            </MenuItem>
            {!user ?
                <>
                    <MenuItem>
                        <Link to="/register">REGISTER</Link>
                    </MenuItem>
                    <MenuItem>
                        <Link to="/login">SIGN IN</Link>
                    </MenuItem>
                </>
                :
                <>
                    <MenuItem onClick={onLogout}>
                        LOGOUT
                    </MenuItem>
                    {user && user.isAdmin && <MenuItem onClick={checkAuth}>
                        DASHBOARD
                    </MenuItem>}
                </>
            }
        </ContainerFlex>
    )
}