import "./sidebar.css";
import { NavLink } from "react-router-dom";
import { useEffect, useState } from "react";
import { constTranslate } from "../../../constant/translate";
import { ConstRoutes } from "../../../constant/routes";


import { LineStyle, PermIdentity, Storefront, PaidOutlined } from "@mui/icons-material";
import MonetizationOnIcon from '@mui/icons-material/MonetizationOn';
import PendingActionsIcon from '@mui/icons-material/PendingActions';
import ChatIcon from '@mui/icons-material/Chat';

export default function Sidebar() {
  const [isSelected, setIsSelected] = useState(window.location.pathname);

  const links = [
    // {
    //   title: constTranslate.DASHBOARD,
    //   list: [
    //     { text: constTranslate.HOME, Icon: LineStyle, link: ConstRoutes.ADMIN }
    //   ]
    // },
    {
      title: constTranslate.USERS,
      list: [
        { text: constTranslate.USERS, Icon: PermIdentity, link: ConstRoutes.ADMIN_USERS },
        { text: constTranslate.BILLING, Icon: PaidOutlined, link: ConstRoutes.ADMIN_USERS_BILLING },
        { text: constTranslate.TOP_UPS, Icon: MonetizationOnIcon, link: ConstRoutes.ADMIN_USERS_CREDITS },
        { text: constTranslate.PENDING_USERS, Icon: PendingActionsIcon, link: ConstRoutes.ADMIN_USERS_PENDING },
        { text: constTranslate.SUPPORT_CHATS, Icon: ChatIcon, link: ConstRoutes.ADMIN_SUPPORT_CHATS },
      ]
    },
    {
      title: constTranslate.AGENT,
      list: [
        { text: constTranslate.AGENT_MESSAGES, Icon: Storefront, link: ConstRoutes.ADMIN_AGENT_MESSAGES },
      ]
    },
    {
      title: constTranslate.CONTACTS,
      list: [
        { text: constTranslate.CONTACTS, Icon: Storefront, link: ConstRoutes.ADMIN_CONTACTS },
        { text: constTranslate.AGENTS_CONTACTS, Icon: MonetizationOnIcon, link: ConstRoutes.ADMIN_CONTACT_REQUEST },
        { text: constTranslate.CONTACTS_FEEDBACK, Icon: Storefront, link: ConstRoutes.ADMIN_FEEDBACKS },
        { text: constTranslate.ADD_COMPANY_CONTACTS, Icon: Storefront, link: ConstRoutes.ADMIN_COMPANY_CONTACTS },
      ]
    },
  ]

  return (
    <div className="sidebar">
      <div className="sidebarWrapper">
        {links.map((link, idx) => {
          return <LinkList key={idx} link={link} selectedRoute={isSelected} setSelectedRoute={setIsSelected} />
        })}
      </div>
    </div>
  );
}


const LinkList = (props) => {
  const { link, selectedRoute, setSelectedRoute } = props

  return (
    <div className="sidebarMenu">
      <h3 className="sidebarTitle">{link.title}</h3>
      {link.list.map((item, idx) => <LinkPreview key={idx} item={item} selectedRoute={selectedRoute} setSelectedRoute={setSelectedRoute} />)}
    </div>
  )
}


const LinkPreview = (props) => {
  const { selectedRoute, setSelectedRoute, item } = props
  const { link, Icon, text } = item

  useEffect(() => {
    if (window.location.pathname === link) setSelectedRoute(link)
  }, [])

  const handleRouteSelect = (route) => {
    setSelectedRoute(link);
  }

  return (
    <NavLink to={link} className="list">
      <li className={`sidebarListItem ${selectedRoute === link ? 'active' : ''}`} onClick={handleRouteSelect}>
        <Icon className="sidebarIcon" />
        {text}
      </li>
    </NavLink>
  )
}