import styled from "styled-components";
import { mobile } from "../../responsive";
import { LoadingButton } from "../Core/LoadingButton";
import { constTranslate } from "../../constant/translate";
import { useState } from "react";
import { userService } from "../../services/user.service";
import { utilService } from "../../services/util.service";
import { showErrorMsg, showSuccessMsg } from "../../services/eventBusService";

const Wrapper = styled.div`
  width: 25%;
  padding: 20px;
  background-color: white;
  ${mobile({ width: "75%" })}
`;

const Title = styled.h1`
  font-size: 24px;
  font-weight: 300;
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
`;

const Input = styled.input`
  flex: 1;
  min-width: 40%;
  margin: 10px 0;
  padding: 10px;
`;

const Error = styled.span`
  color: red;
`;

export const ChangePassword = ({ setType, error, email }) => {
  const [isLoading, setIsLoading] = useState(false)
  const [password, setPassword] = useState({
    matchPasword: '',
    password: ''
  })

  const handleChange = ({ target }) => {
    const { name, value } = target
    setPassword((prevState) => ({ ...prevState, [name]: value }))
  }

  const onSubmit = async (ev) => {
    ev.preventDefault()
    setIsLoading(true)
    if (!utilService.checkPasswordsMatch(password.password, password.matchPasword)) {
      setIsLoading(false)
      return showErrorMsg('Passwords do not match')
    }

    try {
      const res = await userService.changeUserPassByEmail(email, password.password)
      showSuccessMsg("Password changed successfully")
      if (res) setType('login')
    } catch (err) {
      showSuccessMsg(err)
      setIsLoading(false)
    }
  }

  return (
    <Wrapper>
      <Title>Change Password</Title>
      <Form onSubmit={onSubmit}>
        <Input
          placeholder="New Password"
          onChange={handleChange}
          name="password"
          type="password"
        />
        <Input
          placeholder="Confirm Password"
          type="password"
          onChange={handleChange}
          name="matchPasword"
        />
        <LoadingButton isLoading={isLoading}>
          {constTranslate.RESET_PASSWORD}
        </LoadingButton>
        {error.msg && <Error>Something went wrong...</Error>}
      </Form>
    </Wrapper>
  )
}