import { CREDIT_VALUE } from '../config/appConfig'

export const utilService = {
    scrollToTop,
    getFirstLetterUppercase,
    formatMostRecentDate,
    getLastSentence,
    getUserExpensesTotal,
    getContactValueInCredit,
    getAgent,
    getFormattedDate,
    sortDatesByUpdatedFirst,
    getFullDateFormat,
    getOtp,
    isValidEmail,
    checkPasswordsMatch,
    updateObjectById,
    formatTimestamp,
}

function scrollToTop() {
    window.scrollTo(0, 0);
}

function getFirstLetterUppercase(str) {
    return str.charAt(0).toUpperCase() + str.slice(1);
}

function formatMostRecentDate(data) {
    if (!data) return 'Not updated'
    // Map the array of objects to an array of dates
    const dates = data
        .map((obj) => obj.createdAt)
        .filter((dateString) => dateString !== undefined) // Filter out any undefined values
        .map((dateString) => new Date(dateString));

    // Check if there are any dates to work with
    if (dates.length === 0) {
        return undefined; // Or whatever value you want to return if there are no valid dates
    }

    // Find the most recent date
    const mostRecentDate = new Date(Math.max.apply(null, dates));

    // Format the date as dd/m/yyyy
    getFormattedDate(mostRecentDate)
}

function getLastSentence(str) {
    var lastIndex = str.lastIndexOf("/") + 1;
    return str.substring(lastIndex).replace(/-/g, " ");
}

function getUserExpensesTotal(user) {
    if (!user.contactTransactions) return 0
    let amount = 0;
    user.contactTransactions.forEach(transaction => amount += (transaction.priceInCredit * CREDIT_VALUE))
    return amount
}

function getContactValueInCredit(price) {
    return price / CREDIT_VALUE
}

function getAgent(users, fullname) {
    const user = users.find(currUser => currUser.fullname === fullname)
    return { _id: user._id, fullname: user.fullname, imgUrl: user.imgUrl }
}

function getFormattedDate(date) {
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
}

function sortDatesByUpdatedFirst(dates) {
    dates.sort((a, b) => {
        return new Date(b.createdAt) - new Date(a.createdAt);
    });
    return dates;
}

function getFullDateFormat(date) {
    const options = { year: 'numeric', month: 'short', day: 'numeric', hour: 'numeric', minute: '2-digit' };
    const formattedDate = date.toLocaleString('en-US', options);
    const datePart = formattedDate.split(',');
    const [month, year, day] = datePart
    return `${month}, ${year.trim()} at ${day.trim()}`;
}

function getOtp() {
    return Math.floor(Math.random() * 9000 + 1000)
}

function isValidEmail(email) {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
}

function checkPasswordsMatch(password1, password2) {
    return password1 === password2;
}

function updateObjectById(array, id, data) {
    let newArray = [...array]
    const index = newArray.findIndex((obj) => obj._id === id);
    if (index === -1) return newArray
    newArray[index] = { ...newArray[index], ...data };
    return newArray;
}

function formatTimestamp(timestamp) {
    const messageDate = new Date(timestamp);
    const currentDate = new Date();
    // Check if the message is from today
    if (
        messageDate.getDate() === currentDate.getDate() &&
        messageDate.getMonth() === currentDate.getMonth() &&
        messageDate.getFullYear() === currentDate.getFullYear()
    ) {
        // Format the time to be in 12-hour clock and add leading zeros
        const formattedTime = `${messageDate.getHours() % 12 || 12}:${messageDate.getMinutes() < 10 ? '0' : ''}${messageDate.getMinutes()} ${(messageDate.getHours() < 12 ? 'am' : 'pm')}`;

        return `Today ${formattedTime}`;
    } else {
        // If the message is from a different day, you can customize the display format
        return messageDate.toLocaleString(); // Or use any other desired format
    }
}