import { useEffect, useState } from "react"
import { creditTransactionService } from "../../../services/creditTransaction.service"
import { DataGrid } from "@mui/x-data-grid"
import { creditAdminColumns } from "../../../data/adminTables/creditAdminColumns"
import { utilService } from "../../../services/util.service"



export default function CreditAdmin() {
    const [credits, setCredits] = useState([])

    useEffect(() => {
        loadCredits()
    }, [])

    const loadCredits = async () => {
        try {
            const credits = await creditTransactionService.query()
            const sortedCredits = utilService.sortDatesByUpdatedFirst(credits)
            setCredits(sortedCredits)
        } catch (err) {
            console.log(err)
        }
    }

    const getRowId = (row) => {
        return row._id
    }

    return (
        <section className="flex-4">
            <DataGrid
                rows={credits}
                disableSelectionOnClick
                columns={creditAdminColumns}
                pageSize={10}
                checkboxSelection
                getRowId={getRowId}
            />
        </section>
    )
}