import styled from "styled-components";
import { useNavigate, useOutletContext } from "react-router-dom";
import { useState } from "react";
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService";
import { Modal } from "../../../components/Modal";
import { contactService } from "../../../services/contact.service";
import { useEffect } from "react";
import Contact from "../../../components/Contact/Contact";
import { AddContactModal } from "../../../components/Modal/AddContactModal";
import { Agent } from "../../../model/agent/Agent";
import { contactRequestService } from "../../../services/contactRequest.service";
import { constTranslate } from "../../../constant/translate";

const Container = styled.div`
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
`;

const ContactRequirementsPage = () => {
    const { currentUser } = useOutletContext()
    const [modalProps, setModalProps] = useState(null)
    const [contacts, setContacts] = useState([])

    const navigate = useNavigate()

    useEffect(() => {
        loadContacts()
    }, [])

    const loadContacts = async () => {
        try {
            const res = await contactService.query({ inStock: false })
            setContacts(res)
        } catch (err) {
            showErrorMsg('Cannot get contacts')
        }
    }

    const handleContactClick = (ev, item) => {
        ev.stopPropagation()
        setModalProps(item)
    }

    const handleSubmit = async (form) => {
        const res = window.confirm('Are you sure?')
        if (!res) return
        try {
            form.agent = new Agent(currentUser)
            await contactRequestService.createContactRequest(form)
            showSuccessMsg('Your request has been sent!')
            navigate('/')
        } catch (err) {
            showErrorMsg('Cannot send request...')
        }
    }

    return (
        <Container>
            {contacts.map((item) => {
                return <div key={item._id} onClick={(ev) => handleContactClick(ev, item)}>
                    <Contact item={item} />
                </div>
            })}

            {
                modalProps && <Modal setModalProps={setModalProps} style={{ width: '80%', maxWidth: '700px' }} isClosingOutside={false}>
                    <AddContactModal currentUser={currentUser} modalProps={modalProps} isSelectDisabled={true} submitCb={handleSubmit} btnText={constTranslate.SEND_REQUEST} />
                </Modal>
            }
        </Container>
    )
}

export default ContactRequirementsPage