// Functions
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { getAllCategories } from "./redux/categoryManager/apiCalls";
import { getCredits } from "./redux/credit/apiCalls";
import { Route, Routes } from "react-router-dom";
import { ThemeProvider } from "styled-components";
import { breakpoints } from "./constant/style"

// App Routes
import Home from "./pages/Client/Home";
import Register from "./pages/Client/Register";
import Login from "./pages/Client/Login";
import Cart from "./pages/Client/Cart";
import Success from "./pages/Client/Success";
import CreditListPage from "./pages/Client/CreditListPage";
import ContactListPage from "./pages/Client/Contact/ContactListPage";
import ContactRequirementsPage from "./pages/Client/Contact/ContactRequirementsPage";
import ContactDetailsPage from "./pages/Client/Contact/ContactDetailsPage";
import ContactPage from "./pages/Client/Contact/ContactPage";
import ContactFavoritePage from "./pages/Client/Contact/ContactFavoriteFavorite";
import ContactRecommendedPage from "./pages/Client/Contact/ContactRecommendedPage";
import { Redirect } from "./components/Redirect/Redirect";


// Admin Routes
import HomeAdmin from "./pages/admin/adminHome/AdminHome";
import UserList from './pages/admin/userList/UserList'
import User from './pages/admin/user/User'
import NewUser from './pages/admin/newUser/NewUser'
import ContactListAdmin from "./pages/admin/contactList/ContactListAdmin";
import ContactAdmin from "./pages/admin/contact/ContactAdmin";
import NewContact from "./pages/admin/newContact/NewContact";
import CreditAdmin from "./pages/admin/credit/CreditAdmin";
import ContactRequestAdmin from "./pages/admin/contactRequest/ContactRequestAdmin";
import NewContactsCompany from "./pages/admin/newCompanyContacts/NewCompanyContacts";
import DashboardContainer from "./components/admin/dashboardContainer/DashboardContainer";
import AgentMessageList from "./pages/admin/AgentMessageList/AgentMessageList";
import Feedbacks from "./pages/admin/feedbacks/Feedbacks";
import UserSupportChat from "./pages/admin/userSupportChat/UserSupportChat";

// Components
import { ProtectedRoutes } from "./components/ProtectedRoutes";
import { Loader } from "./components/Loader/Loader";
import { UserMsg } from "./components/Core/UserMsg";
import { UserBilling } from "./pages/admin/billing/UserBilling";
import { CreditCart } from "./components/Cart/CreditCart";
import { ContactCart } from "./components/Cart/ContactCart";
import { socketService } from "./services/socket.service";
import { PendingUserList } from "./pages/admin/pendingUserList/PendingUserList";
import { ConstRoutes } from "./constant/routes";


const App = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    socketService.reconnect()
    socketService.connect()

    getAllCategories(dispatch)
    getCredits(dispatch)

    return () => {
      socketService.off()
      socketService.disconnect()
    }
  }, [])

  const theme = {
    breakpoints
  };


  return (
    <ThemeProvider theme={theme}>
      {/* User Routes */}
      <Routes>
        <Route path={ConstRoutes.HOME} element={<Home />} />
        <Route path={ConstRoutes.CONTACT_CATEGORIES} element={<ContactListPage />} />
        <Route path={ConstRoutes.CONTACT_ID} element={<ContactDetailsPage />} />

        {/* Sub User Routes */}
        <Route path={ConstRoutes.CONTACT} element={<ContactPage />} >
          <Route path={ConstRoutes.CONTACT_REQUIREMENTS} element={<ContactRequirementsPage />} />
          <Route path={ConstRoutes.CONTACT_FAVORITE} element={<ContactFavoritePage />} />
          <Route path={ConstRoutes.CONTACT_RECOMMENDED} element={<ContactRecommendedPage />} />
        </Route>

        <Route path={ConstRoutes.CREDITS} element={<CreditListPage />} />
        <Route path={ConstRoutes.CART} element={<Cart />} >
          <Route path={ConstRoutes.CART_CONTACT} element={<ContactCart />} />
          <Route path={ConstRoutes.CART_CREDIT} element={<CreditCart />} />
        </Route>

        <Route path={ConstRoutes.SUCCESS} element={<Success />} />
        <Route path={ConstRoutes.LOGIN} element={<Login />} />
        <Route path={ConstRoutes.REGISTER} element={<Register />} />
        <Route path={ConstRoutes.FALLBACK} element={<Redirect />} />




        {/* Admin Users Only */}
        <Route element={<ProtectedRoutes />}>
          <Route element={<DashboardContainer />}>
            <Route path={ConstRoutes.ADMIN} element={<HomeAdmin />} />
            <Route path={ConstRoutes.ADMIN_USERS} element={<UserList />} />
            <Route path={ConstRoutes.ADMIN_USERS_BILLING} element={<UserBilling />} />
            <Route path={ConstRoutes.ADMIN_USERS_CREDITS} element={<CreditAdmin />} />
            <Route path={ConstRoutes.ADMIN_USERS_PENDING} element={<PendingUserList />} />
            <Route path={ConstRoutes.ADMIN_USER_ID} element={<User />} />
            <Route path={ConstRoutes.ADMIN_NEW_USER} element={<NewUser />} />
            <Route path={ConstRoutes.ADMIN_CONTACTS} element={<ContactListAdmin />} />
            <Route path={ConstRoutes.ADMIN_CONTACT_REQUEST} element={<ContactRequestAdmin />} />
            <Route path={ConstRoutes.ADMIN_CONTACT_CONTACTID} element={<ContactAdmin />} />
            <Route path={ConstRoutes.ADMIN_NEW_CONTACT} element={<NewContact />} />
            <Route path={ConstRoutes.ADMIN_COMPANY_CONTACTS} element={<NewContactsCompany />} />
            <Route path={ConstRoutes.ADMIN_AGENT_MESSAGES} element={<AgentMessageList />} />
            <Route path={ConstRoutes.ADMIN_FEEDBACKS} element={<Feedbacks />} />
            <Route path={ConstRoutes.ADMIN_SUPPORT_CHATS} element={<UserSupportChat />} />
            <Route path={ConstRoutes.FALLBACK} element={<HomeAdmin />} />
          </Route>
        </Route>
      </Routes>
      <Loader />
      <UserMsg />
    </ThemeProvider>
  );
};

export default App;