import { CircularProgress } from '@material-ui/core';
import styled from 'styled-components';

const Button = styled.button`
    width: 100%;
    height: 40px;
    background-color: ${props => props.btnType !== 'secondary' ? '#008080' : '#C8C8C8'};
    color: ${props => props.btnType !== 'secondary' ? '#e9eef8;$' : '#202020'}
    width: 100%;
    height: 40px;
    border-radius: 5px;
    cursor: pointer;
`;

export const LoadingButton = ({ onClick, disabled, isLoading, children, className }) => {

    const handleClick = () => {
        if (onClick) onClick()
    }

    return (
        <Button
            btnType={className}
            disabled={disabled || isLoading}
            onClick={handleClick}
        >
            {isLoading ? (
                <CircularProgress size={24} color="inherit" />
            ) : (
                children
            )}
        </Button>
    );
};
