import { AuthEP } from "../config/appConfig"
import { httpService } from "./http.service"



export const authService = {
    sendRecoveryEmail,
    isValidOTP
}

async function sendRecoveryEmail(email, OTP) {
    try {
        await httpService.post(AuthEP.get, { email })
    } catch (err) {
        throw err
    }
}

async function isValidOTP(OTP) {
    try {
        const res = await httpService.post(AuthEP.check, { OTP })
        if (res.status === 'ok') return res.content
        throw new Error('Invalid OTP')
    } catch (err) {
        throw err
    }
}