import { SupportChatEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const supportChatService = {
    query,
    send
}

export async function query(filterBy = { chatId: '' }) {
    try {
        const res = await httpService.get(SupportChatEP.default, { params: filterBy.chatId })
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get messages')
    } catch (err) {
        throw err
    }
}

export async function send(data) {
    try {
        const res = await httpService.post(SupportChatEP.add, data)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot send message')
    } catch (err) {
        throw err
    }
}