import { useSelector } from "react-redux";
import styled from "styled-components";
import { CREDIT_VALUE } from "../config/appConfig";
import { utilService } from "../services/util.service";
import { userService } from "../services/user.service";
import { useState } from "react";
import { Modal } from "./Modal";
import { constUser } from "../model/user/constUser";
import { ContactSaleModal } from "./Modal/ContactSaleModal";
import { constTranslate } from "../constant/translate";

const Container = styled.div`
  height: 30px;
  background-color: teal;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 14px;
  font-weight: 500;
  padding: 0 20px;
  gap: 20px;
  `;

const Text = styled.span`
  color: white;
`

const Announcement = () => {
  const { currentUser } = useSelector(state => state.user)
  const [modalProps, setModalProps] = useState(null)

  const handleModal = () => {
    if (userService.isAgentUser(currentUser)) {
      setModalProps({ userId: currentUser[constUser._ID] })
    }
  }

  if (!currentUser) return null
  else return (
    <Container>
      <Text>{constTranslate.NAME}: {currentUser.fullname}</Text>
      <Text>{constTranslate.CREDITS}: {currentUser.credits}</Text>
      <Text>{constTranslate.TOTAL_BALANCE}: {currentUser.credits * CREDIT_VALUE}$</Text>
      <Text onClick={handleModal}>{constTranslate.INCOME}: {currentUser.income}$</Text>
      <Text>{constTranslate.EXPENSES}: {utilService.getUserExpensesTotal(currentUser)}$</Text>

      {
        modalProps && <Modal setModalProps={setModalProps} >
          <ContactSaleModal modalProps={modalProps} />
        </Modal>
      }
    </Container>
  )
};

export default Announcement;

