import { useRef } from "react";
import { useEffect } from "react";


export const Modal = ({ children, setModalProps, title, style, isClosingOutside }) => {
    const modalRef = useRef(null)

    useEffect(() => {
        if (isClosingOutside) {
            document.addEventListener('click', eventListener)
            return () => document.removeEventListener('click', eventListener)
        }
    }, [])

    const eventListener = (ev) => {
        if (modalRef.current && !modalRef.current.contains(ev.target)) {
            handleClose()
        }
    }

    const handleClose = () => {
        setModalProps(false)
    };

    return (
        <div className="modal-overlay">
            <div className="modal" ref={modalRef} style={style}>
                {title && <h2>{title}</h2>}
                <div className="modal-close" onClick={handleClose}>X</div>
                {children}
            </div>
        </div>
    )
};