import { DataGrid } from "@mui/x-data-grid"
import { useEffect, useState } from "react"
import { getColumns } from "./AgentMessagesColumn"
import { showErrorMsg } from "../../../services/eventBusService";
import { UserMsg } from "../../../constant/userMessage";
import { agentMessageService } from "../../../services/agentMessage.service";
import { Modal } from "../../../components/Modal"
import { UserAgentMessagesTable } from "./UserAgentMessagesTable";

const AgentMessageList = () => {
    const [users, setUsers] = useState([])
    const [modalProps, setModalProps] = useState(null)

    useEffect(() => {
        loadAgentMessages()
    }, [])

    const loadAgentMessages = async () => {
        try {
            const users = await agentMessageService.getUsersWithAgentMessages()
            setUsers(users)
        } catch (error) {
            showErrorMsg(UserMsg.CANNOT_GET_MESSAGES)
        }
    }

    return (
        <div className="contactList">
            <DataGrid
                rows={users}
                disableSelectionOnClick
                columns={getColumns(setModalProps)}
                getRowId={(row) => row._id}
                pageSize={10}
            />
            {
                modalProps && <Modal setModalProps={setModalProps} style={{ width: '80%', maxWidth: '1200px', height: '400px' }} isClosingOutside={false}>
                    <UserAgentMessagesTable messages={modalProps.messages} />
                </Modal>
            }
        </div>
    )
}


export default AgentMessageList